import React from 'react';
import {Component} from 'react';
import PropTypes from 'prop-types';
const Litmus = require('../../../base/litmus');

import AuthenticatedDropdown from './authenticatedDropdown';
import ReloadButton from './reloadButton';

export default class Header extends Component {

  static get propTypes() {
    return {
      retestEnabled: PropTypes.bool,
      onClickRetest: PropTypes.func,
      recordInSegment: PropTypes.func,
      accounts: PropTypes.arrayOf(PropTypes.object),
      currentAccount: PropTypes.number,
      mode: PropTypes.string,
      admin: PropTypes.bool
    };
  }

  onClickClose(e) {
    e.preventDefault();
    window.parent.postMessage({ method: 'checklist-hide' }, '*');
  }

  onClickLogin(e) {
    e.preventDefault();
    window.parent.postMessage({ method: 'checklist-collapseFullScreen' }, '*');

    Litmus.goToPage('login');
  }

  render() {
    return (
      <header id="litmus-header" className="fixed-top">
        <img src="/images/litmus-logo-wheel.svg" alt="Litmus" className="litmus-header__logo" />

        { this.props.currentAccount !== null ?
          <AuthenticatedDropdown admin={this.props.admin} accounts={this.props.accounts} currentAccount={this.props.currentAccount} redirectUrl={this.props.mode}/>
        : <a className="btn btn-secondary authentication" href="#" onClick={this.onClickLogin.bind(this)}>Log in</a>
        }

        { this.props.retestEnabled ?
          <ReloadButton id="reload-button" className="btn btn-primary retest-button" onClick={this.props.onClickRetest}>
            Refresh
          </ReloadButton>
          : ''
        }
      </header>
    );
  }
}




