import PropTypes from 'prop-types';
import PreviewHeader  from '../../preview/components/previewHeader';

export default class ContentPreviewHeader extends PreviewHeader {

  static propTypes = {
    preview: PropTypes.object,
    expandedFullScreen: PropTypes.bool
  };

	prevDisabled() {
		return true;
	}

	nextDisabled() {
		return true;
	}

	render() {
		const preview = this.props.preview;

		const expandIconClass = this.props.expandedFullScreen ? 'feathericon-minimize' : 'feathericon-maximize';

		return (
			<div className="preview-header">
				<a href="#" className="expand-btn" onClick={this.onClickExpandToggle.bind(this)}>
					<i className={expandIconClass}></i>
				</a>

				<div className="client-container">
					<div className="client">Image Blocking</div>
				</div>

				<a href="#" className="close-action" onClick={this.onClickClose.bind(this)}>Close preview</a>
			</div>
		);
	}

	componentDidMount() {
		jQuery('body').on('keydown.previewHeader', (e) => {
			switch (e.which) {
				case 37: // left
					this.onClickPrev(e);
					break;
				case 39: // right
					this.onClickNext(e);
					break;
			}
		});
	}

	componentWillUnmount() {
		jQuery('body').off('keydown.previewHeader');
	}
}



