import React from 'react';
import { Component } from 'react';
import PropTypes from 'prop-types';

export default class RetestBanner extends Component {

  render() {

    const local = !!(this.props.currentEsp && this.props.currentEsp.name === '');
    if (local) {
      return null;
    }

    return (
      <div id="retest-footer" className={'bottom-footer' + (this.props.visible ? ' banner-open' : ' hidden')}>
        <p>You&apos;ve made changes to your email.&nbsp;
          <a id="retestButton" href="#" onClick={this.props.onClickRetest}>
            Refresh
          </a>
        </p>
      </div>
    );
  }
}

RetestBanner.propTypes = {
  currentEsp: PropTypes.object,
  visible: PropTypes.bool,
  onClickRetest: PropTypes.func
};




