import { Component } from 'react';
import PropTypes from 'prop-types';


export default class EllipsisSpan extends Component {

	static propTypes = {
	  maxLength: PropTypes.number
	}

	render(){
		let text = this.props.children.replace(/^\s+/,'');

		if (text.length > this.props.maxLength){
			let breakPoint = Math.floor((this.props.maxLength - 3) / 2);
			text = text.substr(0, breakPoint) + '...' + text.substr(text.length-breakPoint, text.length);
		}

		return (
			<span {...this.props} >{text}</span>
		);
	}

}



