import React from 'react';
import { Component } from 'react';
import PropTypes from 'prop-types';


export default class ESPSelectItem extends Component {

  constructor(props) {
    super(props);
  }

  onUpgradeClick(e) {
    e.preventDefault();

    let href = this.props.active ? login_url : upgrade_url;
    window.open(href, '_blank');

    if (this.props.active) {
      this.props.recordInSegment('Billing link clicked');
    }
  }

  render() {
    let {name, login_url, img_url, upgrade_url } = this.props;
    return (
      <li className={this.props.active ? '' : 'inactive'}>
        <a href="#" onClick={this.onUpgradeClick.bind(this)}>
          <img src={img_url} alt="" />
          <h4>{name}</h4>
          {this.props.active ? <span>Sign in</span> : '' }
        </a>
      </li>
    );
  }
}

ESPSelectItem.propTypes = {
  name: PropTypes.string,
  login_url: PropTypes.string,
  img_url: PropTypes.string,
  upgrade_url: PropTypes.string,
  active: PropTypes.bool,
  recordInSegment: PropTypes.func
};


