import { Component } from 'react';
import PropTypes from 'prop-types';

export default class FullPreview extends Component {

  static propTypes = {
    expandedFullScreen: PropTypes.bool,
    previews: PropTypes.arrayOf(PropTypes.object),
    index: PropTypes.number,
    retesting: PropTypes.bool,
    imagesEnabled: PropTypes.bool
  };

  constructor() {
    super();
    this.state = {
    	showSpinner: true
    };
  }

  _handleImageLoaded() {
  	this.setState({showSpinner: false});
  }

  onClickExpandToggle(e) {
    e.preventDefault();

    this.props.expandedFullScreen ? this.collapse(true) : this.expand(true);
  }

  expand(persist) {
    window.parent.postMessage({ method: 'checklist-expandFullScreen', persist: !!persist }, '*');
  }

  collapse(persist) {
    window.parent.postMessage({ method: 'checklist-collapseFullScreen', persist: !!persist }, '*');
  }

  render() {
    const preview    = this.props.previews[this.props.index];
    const screenshot = this.props.imagesEnabled ? preview.screenshots.full : preview.screenshots.full_no_images;
    const imageUrl   = screenshot && screenshot.replace(/^\/\//, 'https://');

    let Spinner = this.state.showSpinner
                  ? <div className="spinner-container">
		              <div className="image-spinner"></div>
		            </div> : '';

    return (
      <div className="full-preview">
        <div className="screenshot-container">
          {
          	Spinner
          }
          { preview.ready ?
            <img
              className={'animated fadeIn screenshot' + (this.props.retesting ? ' dim' : '')}
              key={imageUrl}
              src={imageUrl}
              onLoad={this._handleImageLoaded.bind(this)}
              title="Email Screenshot" />
            : '' }
        </div>
        <div className={'preview-hover' + (this.props.expandedFullScreen ? ' expanded' : '')} onClick={this.onClickExpandToggle.bind(this)}></div>
      </div>
    );
  }
}



