import PropTypes                from 'prop-types';
import PreviewPage              from '../preview';
import Header                   from '../../components/header';
import ContentPreviewHeader     from './components/contentPreviewHeader';
import ContentFullPreview       from './components/contentFullPreview';
import RetestBanner             from '../../components/retestBanner';

export default class ContentPreview extends PreviewPage {

  static propTypes = {
    testResult: PropTypes.object,
    accounts: PropTypes.arrayOf(PropTypes.object),
    currentAccount: PropTypes.number,
    expandedFullScreen: PropTypes.bool,
    onClickRetest: PropTypes.func,
    lastInstantApiDate: PropTypes.instanceOf(Date),
    retestEnabled: PropTypes.bool,
    currentEsp: PropTypes.object
  };

  constructor(props) {
    super(props);

    this.state = {
      retesting: false
    };
  }

  onClickRetest(e) {
    e && e.preventDefault();
    this.runRetest();
  }

  runRetest() {
    window.parent.postMessage({ method: 'checklist-runTest' }, '*');
    this.setState({
      retesting: true
    });
  }

	render() {
		// Catch the edge case where we run a retest (clearing the testResult)
	    // but are still in the process of switching back to the checklist page.
	    if (this.props.testResult === null) {
	      return null;
	    }

	    const preview = {url: this.props.testResult.content.images_off_preview_url};

	    return (
	      <div className="checklist">
	        <Header
	          accounts={this.props.accounts}
	          currentAccount={this.props.currentAccount}
	          onClickRetest={this.onClickRetest.bind(this)}
	          retestEnabled={true}
	        />

          <RetestBanner
            onClickRetest={this.onClickRetest.bind(this)}
            currentEsp={this.props.currentEsp}
            visible={this.props.retestEnabled}
          />

	        <ContentPreviewHeader
	          preview={preview}
	          expandedFullScreen={this.props.expandedFullScreen}
	          retestFn={this.props.onClickRetest}
	          lastInstantApiDate={this.props.lastInstantApiDate} />

	        <ContentFullPreview preview={preview} retesting={this.state.retesting} />
	      </div>
	    );
	}
}



