import { Component } from 'react';
import PropTypes from 'prop-types';
import EllipsisSpan from '../ellipsis_span';

export default class LinkRow extends Component {

  static propTypes = {
    status: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    url: PropTypes.string.isRequired
  }

  render() {

    let linkHost, linkPath;
    // If URL doesn't start with http(s), just show the whole thing as the path
    if (!(/^https?:\/\//.test(this.props.url))) {
      linkPath = this.props.url;
    } else {
      const parsedUrl = document.createElement('a');
      parsedUrl.href = this.props.url;
      linkHost = parsedUrl.host;
      linkPath = parsedUrl.pathname.length === 1 ? '' : parsedUrl.pathname;
    }

    let displayLinkPath = (linkHost || '') + ( linkPath || '') ;
    return (
      <div className={'data-row ' + (this.props.status === 'error' ? this.props.status : '')}>
        <table className="data-row-table">
          <tbody>
            <tr className={this.props.status === 'error' ? this.props.status : ''}>
              <th>Link Text:</th>
              <td className={this.props.status === 'error' ? this.props.status : ''}>
                <EllipsisSpan className={ 'filename hint--bottom-right hint--rounded hint--no-animate'} data-hint={this.props.text} maxLength={44}>
                  {this.props.text}
                </EllipsisSpan>
              </td>
            </tr>
            <tr>
              <th>Link Path:</th>
              <td className={this.props.status === 'error' ? this.props.status : ''}>
                <EllipsisSpan className={ 'filename hint--bottom-left hint--rounded hint--no-animate'} data-hint={displayLinkPath} maxLength={44}>
                  {displayLinkPath}
                </EllipsisSpan>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

    );
  }
}



