import { Component } from 'react';
import PropTypes from 'prop-types';

import RetestBanner from '../../components/retestBanner';

import PreviewHeader from './components/previewHeader';
import FullPreview from './components/fullPreview';

export default class PreviewPage extends Component {

  static propTypes = {
    retesting: PropTypes.bool,
    testResult: PropTypes.object,
    path: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    accounts: PropTypes.arrayOf(PropTypes.object),
    currentAccount: PropTypes.number,
    expandedFullScreen: PropTypes.bool,
    lastInstantApiDate: PropTypes.instanceOf(Date),
    onClickRetest: PropTypes.func,
    currentEsp: PropTypes.object,
    retestEnabled: PropTypes.bool,
    fullScreenPreference: PropTypes.bool,
    local: PropTypes.bool.isRequired,
    imagesEnabled: PropTypes.bool,
    recordInSegment: PropTypes.bool
  };

  constructor(props) {
    super(props);

    this.state = {
      retesting: false
    };
  }

  // Custom retest code doesn't clear testResult,
  // just dims current preview
  onClickRetest(e) {
    e.preventDefault();
    let previews = this.props.testResult.previews;
    let index = parseInt(this.props.path);
    let preview  = previews[index];

    let clientId = this.props.testResult.client.id;
    let code = preview.code;

    window.parent.postMessage({ method: 'checklist-retestCurrentClient', currentClient: clientId, code: code }, '*');

    this.setState({
      retesting: true
    });
  }

  retesting() {
    return (this.props.retesting || this.state.retesting);
  }

  stopLoading() {
    this.setState({
      retesting: false
    });
  }

  render() {
    // Catch the edge case where we run a retest (clearing the testResult)
    // but are still in the process of switching back to the checklist page.
    if (this.props.testResult === null) {
      return null;
    }

    const selectedPreviews = this.props.testResult.previews.filter((preview) => (preview.selected && !preview.failed));
    const index = parseInt(this.props.path);

    return (
      <div className="checklist">
        <PreviewHeader
          previews={selectedPreviews}
          index={index}
          retestFn={this.props.onClickRetest}
          expandedFullScreen={this.props.expandedFullScreen}
          imagesEnabled={this.props.imagesEnabled}
          local={this.props.local}
          recordInSegment={this.props.recordInSegment}
          lastInstantApiDate={this.props.lastInstantApiDate} />

        <RetestBanner
          onClickRetest={this.onClickRetest.bind(this)}
          currentEsp={this.props.currentEsp}
          visible={false}
        />

        <FullPreview
          previews={selectedPreviews}
          index={index}
          expandedFullScreen={this.props.expandedFullScreen}
          imagesEnabled={this.props.imagesEnabled}
          retesting={this.retesting()} />
      </div>
    );
  }


  // If the user has previously expanded the detail view to fullscreen, then we save that preference
  // in the state. We fire the expandFullScreen event when the detail view is first loaded
  // (user just clicked on a thumbnail), and also after the whole sidebar was minimized and then reopened.
  detectFullScreenPreference() {
    if (this.props.fullScreenPreference) {
      window.parent.postMessage({ method: 'checklist-expandFullScreen' }, '*');
    }
  }

  componentDidMount() {
    //this.detectFullScreenPreference();
    window.addEventListener('message', this);
  }

  handleEvent(message) {
    if (message.type !== 'message') {
 return;
}

    switch (message.data.method) {
      case 'checklist-expand':
        this.detectFullScreenPreference();
        break;
    }
  }

  componentWillUnmount() {
    window.removeEventListener('message', this);
  }
}



