import { Component } from 'react';
import PropTypes from 'prop-types';

import JumpDropdown from '../../sectionHeader/jumpDropdown';

export default class WhatsNextSection extends Component {
  static propTypes = {
    onClickOpenInLitmus: PropTypes.func,
    skipFirstImpressions: PropTypes.bool,
    testResult: PropTypes.object
  }

  render() {
    return (
      <div className="section whats-next expanded">
        <div>
          <div className="section-heading">
            <div className="section-heading-row top-row">
              <header className="section-title">
                <JumpDropdown text="What's Next" skipFirstImpressions={this.props.skipFirstImpressions} visible={true} testResult={this.props.testResult} />
              </header>
            </div>
          </div>
        </div>

        <div className="section-body animated fadeIn">
        	<div className="panel value-prop">
            <div className="value-block">
              <div className="value-image">
                <img src="/images/icons/proof-icon.png" alt="Collaborate" />
              </div>
              <div className="value-text">
                <h4>Collaborate &amp; accelerate</h4>
                <p>Speed up approvals by sharing and consolidating feedback in a single place.</p>
              </div>
            </div>

            <div className="value-block">
              <div className="value-image">
                <img src="/images/icons/analytics-icon.png" alt="Analytics and Insights" />
              </div>
              <div className="value-text">
                <h4>Track results &amp; analyze engagement</h4>
                <p>Analyze results and gain insights to optimize campaign performance.</p>
              </div>
            </div>

            <div className="value-block">
              <div className="value-image">
                <img src="/images/icons/inbox-icon.png" alt="Reach Inbox" />
              </div>
              <div className="value-text">
                <h4>Reach the inbox</h4>
                <p>Maximize deliverability by testing against every major spam filter before you send.</p>
              </div>
            </div>

            <div className="value-block">
              <div className="value-image">
                <img src="/images/icons/accessibility-icon.png" alt="Accessibility" />
              </div>
              <div className="value-text">
                <h4>Optimize accessibility</h4>
                <p>Confirm your content is accessible by subscribers of all abilities to maximize reach.</p>
              </div>
            </div>

            <a className="btn btn-primary btn-block" href="#" target="_blank" onClick={this.props.onClickOpenInLitmus}>Open in Litmus</a>
          </div>
        </div>
      </div>
    );
  }
}



