// Copied and modified from: http://stackoverflow.com/a/13293684/304706

export default class StickyHeaders {
  static load() {
    const $window = this.$window = $(window);

    const stickies = $('.section-heading');

    if (typeof stickies === 'object' && stickies instanceof jQuery && stickies.length > 0) {
      this.$stickies = stickies;

      this.unload();

      this.calculateHeights();

      $('.sections').on('scroll.stickyheaders', this.onScroll.bind(this));
      this.onScroll();
    }
  }

  static unload() {
     $('.section-heading').removeClass('fixed').removeClass('absolute');

    this.$window.off('scroll.stickyheaders');
  }

  static calculateHeights() {
    // Record offset of first element
    this.firstOffset = this.$stickies.eq(0).offset().top;

    this.originalPositions = [];
    this.originalHeights   = [];

    this.$stickies.each((i, el) => {
      const $thisSticky = $(el),
            height = $thisSticky.outerHeight();

      // Set height on parent element
      $thisSticky.parent().height(height);

      this.originalPositions.push($thisSticky.offset().top);
      this.originalHeights.push(height);

      //console.log("i, originalPosition, originalHeight:", i, $thisSticky.offset().top, height);
    });
  }

  static onScroll() {
    const scrollTop = $('.sections').scrollTop();

    // We need to calculate the scroll speed to reduce
    // flashing when scrolling really fast.
    let scrollSpeed = 0;
    if (this.lastDate && this.lastOffset) {
      const newDate = new Date().getTime();
      const delay = newDate - this.lastDate;
      const offset = scrollTop - this.lastOffset;

      scrollSpeed = Math.abs(offset / delay);

      this.lastDate = newDate;
    } else {
      this.lastDate = new Date().getTime();
    }
    this.lastOffset = scrollTop;

    this.$stickies.each((i, el) => {
      const stickyPosition = this.originalPositions[i];
      const stickyHeight = this.originalHeights[i];
      const $thisSticky = $(el);

      // if (i == 2) {
      //   console.log('position', stickyPosition);
      //   console.log('this.$window.scrollTop() + this.firstOffset', this.$window.scrollTop() + this.firstOffset);
      // }

      var shouldFixStickyHeader = (scrollTop + this.firstOffset) >= stickyPosition;

      if (scrollSpeed > 3.5) {
        shouldFixStickyHeader = (scrollTop + this.firstOffset) >= (stickyPosition + stickyHeight);
      }

      // If the scroll position is greater than the sticky position
      if (shouldFixStickyHeader) {
        $thisSticky.addClass('fixed');
        $thisSticky.parent().next().addClass('fixed');

        const $nextSticky = this.$stickies.eq(i + 1);

        if ($nextSticky.length > 0) {
          const nextPosition = this.originalPositions[i + 1];
          let $nextStickyPosition = nextPosition - stickyHeight;

          // I think jQuery's offset calculation is slightly inaccurate. Maybe floating points.
          if (i > 1) {
            $nextStickyPosition += 2;
          }

          // Only push up the current header when scrolling relatively slowly
          if (scrollSpeed < 3.5) {
            // When the next header is scrolled up, push the current header off the top
            if ($thisSticky.offset().top >= $nextStickyPosition) {
              //$thisSticky.addClass('absolute').css('top', $nextStickyPosition);
            }
          }
        }

      // Scroll position is less than the sticky position
      } else {
        $thisSticky.removeClass('fixed');
        $thisSticky.parent().next().removeClass('fixed');

        const $prevSticky = this.$stickies.eq(i - 1);
        if ($prevSticky.length > 0) {
          const prevHeight = this.originalHeights[i - 1];

          // Maybe a floating point somewhere in Chrome's rendering engine.
          // This fixes a tiny inaccuracy, at least on my computer.
          const offsetFix = (i > 1) ? -1 : 0;
          if ((scrollTop + this.firstOffset + offsetFix) <= (stickyPosition - prevHeight)) {
            $prevSticky.removeClass('absolute').removeAttr('style');
          }
        }
      }
    });

  };
}



