import { Component } from 'react';
import PropTypes from 'prop-types';

export default class TrackingRow extends Component {

  static propTypes = {
    status: PropTypes.string,
    text: PropTypes.string.isRequired,
    source: PropTypes.string,
    medium: PropTypes.string,
    campaign: PropTypes.string,
    type: PropTypes.string
  }

  render() {
    return (
      <div className="data-row">
        <table className={'data-row-table'}>
          <tbody>
            <tr>
              <th>Link Text:</th>
              <td className="link-text">
                {this.props.type === 'Image' ? this.imageLinkText() : this.props.text }
              </td>
            </tr>
            <tr>
              <th>Source:</th>
              <td>{this.props.source || <span>&mdash;</span>}</td>
            </tr>
            <tr>
              <th>Medium:</th>
              <td>{this.props.medium || <span>&mdash;</span>}</td>
            </tr>
            <tr>
              <th>Campaign:</th>
              <td>{this.props.campaign || <span>&mdash;</span>}</td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }

  imageLinkText() {
    return (
      <div className="image-link-text">
        <i className="feathericon-image"></i>
        {this.props.text}
      </div>
    );
  }

}



