import { Component } from 'react';
import PropTypes from 'prop-types';
import ReactTelephoneInput from 'react-telephone-input';


export default class MfaSetupForm extends Component {
  static get propTypes() {
    return {
      onMfaSetupSubmit: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
    this.state = {
      countryCode: 1,
      country: 1,
      phoneNumber: ''
    };
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    this.handleSelectCountry = this.handleSelectCountry.bind(this);
  }

  handleFormSubmit(e) {
    e.preventDefault();

    let number = this.state.phoneNumber.replace(`+${this.state.countryCode}`, '').replace(/ /g,'');

    const formPayload = {
      country_code: this.state.countryCode,
      country: this.state.country,
      phone_number: number
    };
    this.props.onMfaSetupSubmit(formPayload);
  }

  handleSelectCountry(telNumber, selectedCountry) {
    let dialCode = selectedCountry.dialCode;
    this.setState({countryCode: dialCode, phoneNumber: telNumber});
  }

  render() {
    return (
      <form id="mfa_form" onSubmit={this.handleFormSubmit}>
        <div className="form-main">
          <h3>2-step verification has been enabled for this account as an additional measure of security.</h3>
          
          <ReactTelephoneInput
            defaultCountry="us"
            flagsImagePath="/images/flags.png"
            onChange={this.handleSelectCountry}
            placeholder="Please enter your mobile number"
            autoFormat={false}
            />

        </div>

        <div className="multiple no-wrapper">
          <button className="ladda-button btn-primary pull-right" data-style="zoom-out"><span className="ladda-label">Submit</span><span className="ladda-spinner"></span></button>
          <div className="clearfix"></div>
          <div>
            
          </div>
        </div>
      </form>
    );
  }
}



