import PropTypes from 'prop-types';

import BaseSection from '../baseSection';

import IssuesPanel from './issuesPanel';

export default class FirstImpressionsSection extends BaseSection {
  static sectionName = 'First Impressions'
  static sectionSlug = 'firstImpressions'


  static propTypes = {
    from: PropTypes.object.isRequired,
    replyTo: PropTypes.object.isRequired,
    previewText: PropTypes.object.isRequired,
    subjectLine: PropTypes.object,
    userType: PropTypes.string
  }

  render() {
    let params = this.props;

    // if no first impression data is passed
    if (!params.from.display_name && !params.replyTo.email_address && !params.previewText.preview_text && !params.subjectLine.subject_text) {
      return false;
    }

    if (this.props.visible === false) {
      return null;
    }

    const classNames = [
      'section',
      this.constructor.sectionSlug,
      this.props.approved ? 'collapsed' : 'expanded'
    ].join(' ');

    return (
      <div className={classNames}>
        { this.props.local ? '' : this.renderHeader() }

        { this.props.approved ? '' : this.renderSectionBody() }
      </div>
    );
  }

  renderSectionBody() {

  	let messageForActivationCTA = 'Get your subscribers to open your email-ensure you are making a good first impression in the Inbox.';
    let paidPlan = this.props.userType === 'paidUser';

    let subjectPanel = this.props.subjectLine.subject_text ? 
      <IssuesPanel
        title="Subject Line"
        content={this.props.subjectLine.subject_text}
        result={this.props.subjectLine.result} /> : '';

    let fromPanel = this.props.from.display_name ? 
      <IssuesPanel
        title="From Name"
        content={this.props.from.display_name}
        result={this.props.from.result} /> : '';

    let replyToPanel = this.props.replyTo.email_address ? 
      <IssuesPanel
        title="Reply-to"
        content={this.props.replyTo.email_address}
        result={this.props.replyTo.result} /> : '';

    let previewPanel = this.props.previewText.preview_text ? 
      <IssuesPanel
        title="Preview Text"
        content={this.props.previewText.preview_text}
        result={this.props.previewText.result} /> : '';

    return (
      <div className="section-body animated fadeIn">
		{
			this.renderActivationCTA(messageForActivationCTA, this.props.userType)
		}
		<div className={ paidPlan ? '' : 'blurred-content' }>
	        {subjectPanel}
	        {fromPanel}
	        {replyToPanel}
	        {previewPanel}
	    </div>
      </div>
    );
  }

}



