import { Component } from 'react';
import PropTypes from 'prop-types';
const Litmus = require('../../../../base/litmus');

import AccountButton from './components/accountButton';

export default class AccountsPage extends Component {

  static propTypes = {
    mode: PropTypes.string,
    accounts: PropTypes.arrayOf(PropTypes.object),
    currentAccount: PropTypes.number
  };

  onClickClose(e) {
    e.preventDefault();
    Litmus.goToPage(this.props.mode);
  }

  onClickAccountButton(index, e) {
    e.preventDefault();
    window.parent.postMessage({
      method: 'checklist-updateCurrentAccount',
      current_account: index
    }, '*');
    // window.location.hash = this.props.mode;
  }

  render() {
    return (
      <div className="page-content accounts">
        <a href="#" className="close feathericon-cross" onClick={this.onClickClose.bind(this)}></a>

        <h3>Select an account to load:</h3>

        <div className="account-buttons">
          { (this.props.accounts || []).map((account, i) =>
            <AccountButton
              selected={i === this.props.currentAccount}
              accountName={account.account_name}
              onClick={this.onClickAccountButton.bind(this, i)}
              key={i}
            />
          )}
        </div>
      </div>
    );
  }
}




