import { Component } from 'react';
import PropTypes from 'prop-types';

import IssueRow from './issueRow';

export default class IssuesPanel extends Component {

  static propTypes = {
    title: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired,
    result: PropTypes.object.isRequired
  }

  render() {
    const issueRows = [];

    if (!this.props.result.issues.length) {
      if (this.props.result.comments) {
        issueRows.push({status: 'success', value: this.props.result.comments});
      } else {
        // No comment, return nothing.
        return null;
      }
    } else {
      this.props.result.issues.forEach(issue =>
        issueRows.push({status: 'error', value: issue.value})
      );
    }

    return (
      <div className="panel issues-panel">
        <div className="issue-details">
          <h3>{this.props.title}</h3>
          <div className="content">{this.props.content || <span>&mdash;</span>}</div>
        </div>
        <table className="issues">
          <tbody>
            {issueRows.map((issue, i) => <IssueRow {...issue} key={i} />)}
          </tbody>
        </table>
      </div>
    );

  }

}



