import React from 'react';
import { Component } from 'react';
import PropTypes from 'prop-types';

import AuthenticatedMenu from './authenticatedMenu';
import enhanceWithClickOutside from 'react-click-outside';

class AuthenticatedDropdown extends Component {

  constructor(props) {
    super(props);

    this.state = {
      hover: false
    };
  }

  handleClickOutside(e) {
    this.setState({hover: false});
  }

  onClick() {
    this.setState({hover: !this.state.hover});
  }

  render() {
    const account = this.props.accounts[this.props.currentAccount];

    return (
      <div className="settings-dropdown dropdown" tabIndex={10}  onClick={this.onClick.bind(this)}>
        <a className="settings" title="Settings" data-login-required="true" >
          <span className="text">{ account.account_name }</span>
          <span className="icon icon-chevron-down"></span>
        </a>

        { this.state.hover ? <AuthenticatedMenu redirectUrl={this.props.redirectUrl} admin={this.props.admin} recordInSegment={this.props.recordInSegment} /> : '' }
      </div>
    );
  }
}

AuthenticatedDropdown.propTypes = {
  accounts: PropTypes.arrayOf(PropTypes.object),
  currentAccount: PropTypes.number,
  redirectUrl: PropTypes.string.isRequired,
  admin: PropTypes.bool,
  recordInSegment: PropTypes.func
};

export default enhanceWithClickOutside(AuthenticatedDropdown);



