import { Component } from 'react';
import PropTypes from 'prop-types';

export default class NewSecondFactorForm extends Component {
  static get propTypes() {
    return {
      onNewCodeRequest: PropTypes.func,
      onCodeSubmit: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
    this.state = {
      token: ''
    };
    this.handleTokenChange = this.handleTokenChange.bind(this);
    this.handleNewCodeRequest = this.handleNewCodeRequest.bind(this);
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
  }

  handleTokenChange(e) {
    this.setState({token: e.target.value});
  }

  handleNewCodeRequest(e) {
    e.preventDefault();
    this.props.onNewCodeRequest();
  }

  handleFormSubmit(e) {
    e.preventDefault();
    const formPayload = {
      token: this.state.token
    };
    this.props.onCodeSubmit(formPayload);
  }

  render() {
    return (
      <form onSubmit={this.handleFormSubmit}>
        <div className="form-main">
          <h3>We've just sent a verification code to your phone</h3>
          <div className="form-row">
            <label htmlFor="token">Please enter your code below:</label>
            <div className="input-group">
              <input
                className="mfa-token"
                name="token"
                type="text"
                value={this.state.token}
                onChange={this.handleTokenChange}
                autoFocus="true"
                required="true" />
            </div>
          </div>
        </div>

        <div className="multiple no-wrapper" style={{height: '100px'}}>

          <button className="btn btn-primary pull-right">Verify</button>

          <div className="pull-left">
            <a className="btn btn-link pull-left mfa-link" onClick={this.handleNewCodeRequest}>Send me a new code</a>
            <div className="clearfix"></div>
          </div>

          <div className="clearfix"></div>
        </div>
      </form>
    );
  }
}


