import { Component } from 'react';
import PropTypes from 'prop-types';
import ReadableFileSize from './readableFileSize';

export default class SummaryPanel extends Component {

  static propTypes = {
    images_result: PropTypes.object.isRequired,
    loadTime: PropTypes.number
  }

  render() {
    const totalImages = this.props.images_result.image_checks.length;
    const totalSize = Math.round(
      this.props.images_result.image_checks
        .map(c => c.size)
        .reduce(((a, b) => a + b), 0)
    );
    const readableSize = ReadableFileSize.calculate(totalSize);

    return (
      <div className="panel summary-panel">
        <table className="summary">
          <tbody>
            <tr>
              { this.summaryPanelColumn('Images', totalImages) }
              { this.summaryPanelColumn('Total size', readableSize.size, readableSize.unit) }
              { this.summaryPanelColumn('Load time', this.props.loadTime, 'S') }
            </tr>
          </tbody>
        </table>
      </div>
    );
  }

  summaryPanelColumn(description, value, unit) {
    return (
      <td>
        <div className="value-container">
          <div className="value">{value}</div>
          <div className="value-unit">{unit}</div>
        </div>

        <div className="description">{description}</div>
      </td>
    );
  }

}



