import { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../../../components/header';
const Litmus = require('../../../../../base/litmus');

export default class LoggedOutUpsellPage extends Component {
  static get propTypes() {
    return {
      currentAccount: PropTypes.number,
      accounts: PropTypes.arrayOf(PropTypes.object),
      mode: PropTypes.string,
      currentEsp: PropTypes.object.isRequired,
      calloutLink: PropTypes.string.isRequired,
      recordInSegment: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  onClickSignup(e) {
    e.preventDefault();
    window.open(this.props.calloutLink, '_blank');

    this.props.recordInSegment('Signup clicked');
  }

  onClickLogin(e) {
    e.preventDefault();
    window.parent.postMessage({ method: 'checklist-collapseFullScreen' }, '*');

    Litmus.goToPage('login');
  }

  render() {
    let espCallout = this.props.currentEsp && this.props.currentEsp.name ? `Testing inside ${this.props.currentEsp.name}` : 'Testing';
    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          mode={this.props.mode}
        />

        <div className="sections">
          <div className="section dashboard expanded">
            <div className="section-body animated fadeIn">
              <div className="free-trial-message">

                <h1>Test with Litmus wherever you build</h1>

                <div className="playerContainer">
                  <iframe src="https://fast.wistia.net/embed/iframe/azplvsa127" width="100%" height="233px" style={{ position: 'absolute', top: 0, bottom: 0, left: 0, right: 0 }}/>
                </div>

                <p>Litmus Extension brings the power of Litmus testing to your email workflow, allowing you to check email elements that are essential in creating an exceptional subscriber experience no matter where you build.</p>

                <ul className="feature-list">
                  <li>Easily detect and fix issues early in your email creation workflow, saving time and boosting email accuracy.</li>
                  <li>Test email previews and other elements crucial to email success including subject line, preview text, analytics tracking, image blocking, loading speed, and link functionality.</li>
                  <li>Several ESPs (SFMC, Klaviyo, Marketo, Responsys, Adobe Campaign Standard) support dynamic content testing via the Litmus Extension.</li>
                </ul>

                <p>
                  The Litmus Extension works with:
                </p>

                <div className="feature-list">
                  <ul>
                    <li>Acoustic Campaign</li>
                    <li>Adobe Campaign Standard</li>
                    <li>Campaign Monitor</li>
                    <li>Eloqua</li>
                    <li>Klaviyo</li>
                    <li>MailChimp</li>
                    <li>Marketo</li>
                    <li>Pardot</li>
                    <li>Responsys</li>
                    <li>Salesforce Marketing Cloud (formerly ExactTarget)</li>
                    <li>All editors, task runners, and local hosts, such as Dreamweaver and Sublime Text</li>
                  </ul>
                </div>

                <a className="btn btn-primary btn-lg btn-block" href="#" onClick={this.onClickSignup.bind(this)}>
                  <span className="button-text">Learn more + sign up today →</span>
                </a>

                <p>Already have an account? <a href="#" onClick={this.onClickLogin.bind(this)}>Log in</a>.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



