var React = require('react');
import { Component } from 'react';
import PropTypes from 'prop-types';

import JumpDropdown from './jumpDropdown';

export default class SectionHeader extends Component {

  constructor(props) {
    super(props);

    this.state = {
      showUnapprove: false
    };
  }

  // Only show unapprove button if section is approved
  componentWillReceiveProps(nextProps) {
    if (!nextProps.approved) {
      this.setState({
        showUnapprove: false
      });
    }
  }

  onClickHeader(e) {
    // Toggle "unapprove" button when clicking the header of an approved section
    if (this.props.approved) {
      this.setState({
        showUnapprove: !this.state.showUnapprove
      });
    }
  }

  renderIssuesCount() {
    if (!this.props.issuesCount) {
      return;
    }

    return (
      <div className="issues-count">
        <span className="icon-warning" />
        <span className="count">{ this.props.issuesCount }</span>
      </div>
    );
  }

  render() {
    const headingClassName = ['section-heading'];

    if (this.props.approved)      {
headingClassName.push('collapsed');
}

    return (
      <div>
        <div className={headingClassName.join(' ')} onClick={this.onClickHeader.bind(this)}>
          <div className="section-heading-row top-row">
            <header className="section-title">
              <JumpDropdown text={this.props.name} skipFirstImpressions={this.props.skipFirstImpressions} visible={!this.props.local} testResult={this.props.testResult} />
            </header>

            { this.props.rightSectionContent || this.renderIssuesCount() }
          </div>
        </div>
      </div>
    );
  }

  componentDidUpdate() {
    // Header height changed, fire event on parent so that sticky headers are updated
    this.props.onHeaderUpdate();
  }

}

SectionHeader.propTypes = {
  approved: PropTypes.bool.isRequired,
  toggleApproved: PropTypes.func.isRequired,
  slug: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  local: PropTypes.bool,
  issuesCount: PropTypes.number,
  skipFirstImpressions: PropTypes.bool,
  rightSectionContent: PropTypes.object,
  onHeaderUpdate: PropTypes.func,
  testResult: PropTypes.object
};



