import { Component } from 'react';

import SectionBox from './components/sectionBox';
const Litmus  = require('../../../../base/litmus');

export default class CompletePage extends Component {
  onClickClose(e) {
    e.preventDefault();
    Litmus.goToPage('checklist');
  }

  render() {
    return (
      <div className="page-content complete">
        <a href="#" className="close feathericon-cross" onClick={this.onClickClose.bind(this)}></a>

        <div className="headerContent">
          <h1>You&apos;re ready to send!</h1>
          <h3>We&apos;ve analyzed your email against hundreds of best practices and common mistakes.</h3>
        </div>

        <div className="sectionBoxes">
          <SectionBox text="Previews" />
          <SectionBox text="First Impressions" />
          <SectionBox text="Content" />
          <SectionBox text="Links" />
          <SectionBox text="Tracking" />
          <SectionBox text="Images" />
        </div>

        <div className="middleContent">
          <h3>Now that you&apos;re ready to send, here&apos;s what you can do next.</h3>
        </div>

        <div className="upsells">
          <div className="upsell-section">
            <p>Want to see how this email performs?</p>
            <a className="btn green" href="https://litmus.com/analytics" target="_blank">
              Set up monitoring for this campaign
            </a>
          </div>

          <span className="divider-text">Or</span>

          <div className="upsell-section">
            <p>Check deliverability of your emails.</p>
            <a className="btn grey" href="https://litmus.com/spam" target="_blank">
              Start a spam test
            </a>
          </div>
        </div>
      </div>
    );
  }
}



