import { Component } from 'react';
import PropTypes from 'prop-types';


export default class TrialLimitMessage extends Component {

  onCalloutClick(e) {
    e.preventDefault();

    window.open(this.props.calloutLink, '_blank');
    this.props.recordInSegment('Billing link clicked');
  }

  render(){
    return (
      <div className={`trial-usage ${this.props.containerClass}`}>
        <h1 className="usage-title">Monthly Email Previews usage</h1>
        <small>{this.props.message} <a href="#" onClick={this.onCalloutClick.bind(this)}>{this.props.linkText}</a></small>
        <p className="limit-counter">
          <strong>{this.props.count}</strong> of <strong>{this.props.totalLimit}</strong>
        </p>
      </div>
    );
  }
}

TrialLimitMessage.propTypes = {
  message: PropTypes.string.isRequired,
  count: PropTypes.number.isRequired,
  totalLimit: PropTypes.number.isRequired,
  containerClass: PropTypes.string,
  calloutLink: PropTypes.string,
  linkText: PropTypes.string,
  recordInSegment: PropTypes.func
};



