import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Tooltip, OverlayTrigger } from 'react-bootstrap';

export default class ImageToggleButton extends Component {
  render() {
    let tooltip = <Tooltip id={this.props.id}>{this.props.tooltip}</Tooltip>;

    return (
      <OverlayTrigger
        overlay={tooltip} placement="bottom"
        delayShow={300} delayHide={150} positionLeft={500}
      >
        <a className={this.props.className} onClick={this.props.onClick}>{this.props.children}</a>
      </OverlayTrigger>
    );
  }
};

ImageToggleButton.propTypes = {
  id: PropTypes.string,
  tooltip: PropTypes.string,
  className: PropTypes.string,
  onClick: PropTypes.func
};


