import { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../../../components/header';

export default class EnterpriseCtaPage extends Component {
  static get propTypes() {
    return {
      currentAccount: PropTypes.number,
      accounts: PropTypes.arrayOf(PropTypes.object),
      mode: PropTypes.string,
      currentEsp: PropTypes.object.isRequired,
      recordInSegment: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  onEnterpriseClick(e) {
    e.preventDefault();

    window.open('https://litmus.com/enterprise?utm_medium=litmusextension#contact', '_blank');
    this.props.recordInSegment('Enterprise site clicked');
  }

  render() {
    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          mode={this.props.mode}
        />

        <div className="sections">
          <div className="section dashboard expanded">
            <div className="section-body animated fadeIn">
              <div className="free-trial-message">
                <h1>Good Morning!</h1>
                <p>{`Testing inside ${this.props.currentEsp.name} with the Litmus Extension is only available on Enterprise plans. Please contact us to learn more.`}</p>
                <a className="btn btn-primary btn-lg btn-block" href="#" onClick={this.onEnterpriseClick.bind(this)}>
                  <span className="button-text">Get in touch</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


