import { Component } from 'react';
import PropTypes from 'prop-types';
import ReadableFileSize from './readableFileSize';
import EllisipSpan from '../ellipsis_span';
import decimalAdjust from './decimalAdjust';

export default class ImageRow extends Component {

  static propTypes = {
    check: PropTypes.object.isRequired
  }

  render() {
    const readableSize = ReadableFileSize.calculate(this.props.check.size);

    const status = (this.props.check.errored || this.props.check.timed_out) ? 'error' : 'success';

    const startTime = new Date(this.props.check.started);
    const endTime = new Date(this.props.check.completed);
    const time = decimalAdjust('ceil', (endTime - startTime) / 1000, -1);

    let host, pathRoot, filename;

    let imageURL = this.props.check.resource;
    const mailchimpProxyRegex = /^https?:\/\/[a-z0-9]+\.cloudfront\.net\/_ssl\/proxy\.php\/(https?)\//;

    if (mailchimpProxyRegex.test(imageURL)) {
      imageURL = imageURL.replace(mailchimpProxyRegex, '$1://');
    }

    let urlParts = imageURL.split('/');

    // Quick workaround to split longer URLs, since this is hard with the hint.css library
    let multilineImageURL = imageURL.replace(/(.{36})/g,'$1\u000A');

    return (
      <tr className={status === 'error' ? status : ''}>
        <td className="link-path">
          {
          	imageURL.indexOf('/') !== -1
          	    ? <EllisipSpan className={'filename hint--bottom-rigth hint--rounded hint--no-animate ' + (status === 'error' ? status : '')} data-hint={multilineImageURL} maxLength={22}>{urlParts[urlParts.length - 1]}</EllisipSpan>
                : <EllisipSpan className={'filename ' + (status === 'error' ? status : '')}>{imageURL}</EllisipSpan>
          }
        </td>
        <td className="image-size">
          <span className={status === 'error' ? status : ''}>{readableSize.size}{readableSize.unit}</span>
        </td>
        <td className="image-time">
          <span className={status === 'error' ? status : ''}>{time}s</span>
        </td>
      </tr>
    );
  }

}



