import React from 'react';
import BaseApp from '../../base/client/app';

import Litmus from '../../base/litmus';

import ChecklistPage from './pages/checklist';
import LoginPage from './pages/login';
import AccountsPage from './pages/accounts';
import PreviewPage from './pages/preview';
import ContentPreviewPage from './pages/contentPreview';
import CompletePage from './pages/complete';
import DashboardPage from './pages/dashboard';
import NewSecondFactorPage from './pages/newSecondFactor';
import MfaSetupPage from './pages/mfaSetup';
import EnterpriseCtaPage from './pages/ctas/enterpriseCta';
import LoggedOutUpsellPage from './pages/ctas/loggedOutUpsell';
import ExtensionUpgradeCtaPage from './pages/ctas/extensionUpgradeCta';
import EspUpgradeCtaPage from './pages/ctas/espUpgradeCta';
import OverUsageCtaPage from './pages/ctas/overUsageCta';
import ReadOnlyCtaPage from './pages/ctas/readOnlyCta';

export default class App extends BaseApp {
  constructor(props) {
    super(props);

    this.state = {
      collapsed: true,
      expandedFullScreen: false,
      fullScreenPreference: false,
      showAllPreviews: false,
      loggedIn: false,
      freeAccount: false,
      currentAccount: null,
      accounts: [],
      currentPage: null,
      retesting: false,
      wide: false,
      local: false,
      imagesEnabled: true,
      init: true,

      testResult: null,
      firstPreviewsReady: false,
      waitingForFirstPreviews: true,
      reportId: null,
      lastRunDate: null,
      lastInstantApiDate: null,
      retestEnabled: false,
      currentEsp: {},
      fileAccess: true,
      calloutLink: null,
      skipFirstImpressionsSection: false,
      parentUrl: null,

      trial: false,
      hasAccess: false,
      planGroup: null,
      previewsCount: 0,
      totalLimit: 0,
      testId: null,
      espTestId: null,

      mfaError: '',
      mfaSuccess: false,
      mfaNewCode: false,

      approvedSections: {
        previews: false,
        firstImpressions: false,
        content: false,
        links: false,
        tracking: false,
        images: false,
        customChecklist: false,
        spam: false
      }
    };
  }

  toggleApproved(sectionName, e) {
    e.preventDefault();

    const newApprovedSections = {...this.state.approvedSections};
    newApprovedSections[sectionName] = !this.state.approvedSections[sectionName];

    this.setState({
      approvedSections: newApprovedSections
    });

    // If all sections are now approved, go to the 'complete' page
    if (Object.keys(newApprovedSections).every((k) => {
      return newApprovedSections[k];
    })) {
      Litmus.goToPage('complete');
    }
  }

  onClickShowAllPreviews(e) {
    e.preventDefault();

    this.setState({
      showAllPreviews: true
    });
    this.recordInSegment('View all previews clicked');
  }

  unapproveChangedSections(newResult) {
    if (!this.state.testResult) {
      return;
    }

    const newApprovedSections = {...this.state.approvedSections};

    const keyChanged = (key) => {
      return JSON.stringify(this.state.testResult[key]) !== JSON.stringify(newResult[key]);
    };

    let sectionListeners = {
      'previews' : ['previews'],
      'firstImpressions' : ['from']
    };

    if (keyChanged('previews')) {
      newApprovedSections['previews'] = false;
    }

    if (keyChanged('from') || keyChanged('reply_to') ||
        keyChanged('preview_text') || keyChanged('subject_line')) {
      newApprovedSections['firstImpressions'] = false;
    }

    if (keyChanged('content')) {
      newApprovedSections['content'] = false;
    }

    if (keyChanged('bad_links') || keyChanged('good_links')) {
      newApprovedSections['links'] = false;
    }

    if (keyChanged('tracking')) {
      newApprovedSections['tracking'] = false;
    }

    if (keyChanged('images')) {
      newApprovedSections['images'] = false;
    }

    if (keyChanged('spam')) {
      newApprovedSections['spam'] = false;
    }

    if (keyChanged('customChecklist')) {
      newApprovedSections['customChecklist'] = false;
    }

    this.setState({
      approvedSections: newApprovedSections
    });
  }


  render() {
    const Page = this.state && this.state.page;
    const pageKey = [this.state.lastRunDate, this.state.lastInstantApiDate].join('/');
    const retesting = this.state.retesting;

    let espClass;
    if (this.state.currentEsp && this.state.currentEsp.name) {
      // Add ESP class to the content div inside the sidebar iframe
      const espName = this.state.currentEsp.name.toLowerCase().replace(/\W+/, '-');
      espClass = 'esp-' + espName;
    } else {
      espClass = 'no-esp';
    }

    return (
      <div id="content" className={espClass}>
        {Page ?
          <Page
            key={pageKey}
            ref="page"
            {...this.state}
            toggleApproved={this.toggleApproved.bind(this)}
            onClickRetest={this.onClickRetest.bind(this)}
            onClickShowAllPreviews={this.onClickShowAllPreviews.bind(this)}
            recordInSegment={this.recordInSegment.bind(this)}
            retestingVal={retesting}
          />
        : ''}
      </div>
    );
  }


  willLeavePage(pageName) {
    // console.log("will leave page: '" + pageName + "'");

    // Save and restore scroll position for main page
    if (!pageName || pageName === 'checklist') {
      //console.log("storing scroll y:", window.scrollY);
      this.setState({
        checklistScrollY: window.scrollY
      });
    }
  }

  didUpdatePage(pageName) {
    // console.log("did update page: '" + pageName + "'");
    this.setState({currentPage: pageName});

    if (!pageName || pageName === 'checklist') {
      // console.log("scrolling to:", this.state.checklistScrollY || 0);
      window.scrollTo(0, this.state.checklistScrollY || 0);
    } else {
      // Otherwise just scroll to the top of the window when showing a new page
      // console.log("scrolling to 0");
      window.scrollTo(0, 0);
    }
  }

  getPageClassByName(pagename) {
    let PageClass;

    switch (pagename) {
    case 'login':
      PageClass = LoginPage;
      break;
    case 'accounts':
      PageClass = AccountsPage;
      break;
    case 'preview':
      PageClass = PreviewPage;
      break;
    case 'contentPreview':
      PageClass = ContentPreviewPage;
      break;
    case 'complete':
      PageClass = CompletePage;
      break;
    case 'checklist':
      PageClass = ChecklistPage;
      break;
    case 'mfa-setup':
      PageClass = MfaSetupPage;
      break;
    case 'new-second-factor':
      PageClass = NewSecondFactorPage;
      break;
    case 'enterpriseCta':
      PageClass = EnterpriseCtaPage;
      break;
    case 'loggedOutUpsell':
      PageClass = LoggedOutUpsellPage;
      break;
    case 'extensionUpgradeCta':
      PageClass = ExtensionUpgradeCtaPage;
      break;
    case 'espUpgradeCta':
      PageClass = EspUpgradeCtaPage;
      break;
    case 'overUsageCta':
      PageClass = OverUsageCtaPage;
      break;
    case 'readOnlyCta':
      PageClass = ReadOnlyCtaPage;
      break;
    default:
      PageClass = DashboardPage;
      break;
    }

    return PageClass;
  }

  componentWillMount() {
    this.setState({
      reportId: null
    });

    if (window.location.hash === '#checklist') {
      this.setState({mode: 'checklist'});
    }

    if (window.location.hash === '') {
      this.setState({mode: ''});
    }

    window.addEventListener('message', this);

    // Determine whether the user is logged in or not
    window.parent.postMessage({ method: 'checklist-loginStatus' }, '*');

    // Fetch the current ESP
    window.parent.postMessage({ method: 'checklist-getCurrentEsp' }, '*');

    // Fetch parent Url
    window.parent.postMessage({ method: 'checklist-fetchParentUrl' }, '*');

    // Run tests
    //window.parent.postMessage({ method: 'checklist-runTest' }, '*');

    // Check if we should automatically run the test
    window.parent.postMessage({ method: 'checklist-checkAutoRunTest' }, '*');
  }

  componentWillUnmount() {
    this.setState({
      reportId: null
    });

    window.removeEventListener('message', this);
  }

  onClickRetest(e) {
    e && e.preventDefault();
    this.runRetest();

    this.recordInSegment('Retest clicked');
  }

  runRetest() {
    window.parent.postMessage({ method: 'checklist-runTest' }, '*');
    this.setState({
      testResult: null,
      firstPreviewsReady: false,
      waitingForFirstPreviews: true,
      retestEnabled: false,
      errorType: null
    });

    this.recordInSegment('run_retest');
  }

  triggerLoginEvents() {
    let token = '';
    let account  = (this.state.accounts && this.state.currentAccount !== null) ? this.state.accounts[this.state.currentAccount] : null;
    if (account != null) {
      token = account.token;
    }

    let payload = {
      analyticsEventType: "identify",
      traits: {
        api_token: token,
        languages: window.navigator.languages
      }
    }
    chrome.runtime.sendMessage({ type: "send_analytics", data: payload});

    this.recordInSegment('extension_added_to_page');

  }

  loginStatus(loggedIn, currentAccount, accounts, newLogin = false) {
    this.setState({
      loggedIn: loggedIn,
      currentAccount: currentAccount,
      accounts: accounts,
      enterprise: accounts[currentAccount] && accounts[currentAccount].enterprise
    });

    this.triggerLoginEvents();
    this.setCalloutLink();

    // if logged out, go straight to dashboard pane, or show upsell if in an esp
    if (!loggedIn) {
      window.postMessage({ method: 'checklist-show-logged-out-upsell' }, '*');
    } else if (loggedIn && accounts[currentAccount].user_read_only) {
      window.postMessage({ method: 'checklist-show-read-only-cta' }, '*');
    } else if (loggedIn && this.state.currentEsp && this.state.currentEsp.active) {
      if (newLogin) {
        window.postMessage({ method: 'checklist-show-checklist' }, '*');
      }
    } else if (loggedIn && !accounts[currentAccount].extension_enabled) {
      window.postMessage({ method: 'checklist-show-extension-upgrade-cta' }, '*');
    } else {
      window.postMessage({ method: 'checklist-show-dashboard' }, '*');
    }

    // If on basic plan, show upsell

    // Always re-run test when login status is updated, except if this is the initial page load
    if (!$('#inject-litmus-checklist').hasClass('collapsed') && !this.state.init && this.state.mode === 'checklist') {
      this.runRetest();
    }
    this.setState({ init: false });
  }

  handleEvent(message) {
    if (message.type !== 'message') {
      return;
    }

    switch (message.data.method) {
      case 'checklist-expand':
        this.setState({collapsed: false});
        this.recordInSegment('checklist_expand');
        break;

      case 'checklist-collapse':
        this.setState({collapsed: true});
        this.recordInSegment('checklist_collapse');
        break;

      case 'login-status':
        this.loginStatus(message.data.loggedIn, message.data.currentAccount, message.data.accounts, message.data.newLogin);
        break;

      case 'checklist-currentEsp':
        this.setState({
          currentEsp: message.data.currentEsp,
          espUri: message.data.espUri
        });
        break;

      case 'checklist-currentParentUrl':
        this.setState({
          parentUrl: message.data.currentUrl
        });
        this.setCalloutLink();
        break;

      case 'checklist-segmentInfo':
        this.setState({
          currentUri: message.data.segmentInfo.currentUri
        });
        break;

      case 'checklist-imagesEnable':
        this.setState({
          imagesEnabled: true
        });
        break;

      case 'checklist-imagesDisable':
        this.setState({
          imagesEnabled: false
        });
        break;

      case 'checklist-retest':
        if (message.data.previews_only) {
          window.parent.postMessage({ method: 'checklist-runTest', previews_only: true }, '*');
        } else if (this.state.currentPage === 'preview') {
          this.setState({retesting: true});
          window.parent.postMessage({ method: 'checklist-runTest' }, '*');
        } else {
          this.runRetest();
        }
        break;

      case 'checklist-checklistPage':
        let Page = this.getPageClassByName('checklist');
        this.setState({
          page: Page
        });
        window.parent.postMessage({ method: 'checklist-runTest' }, '*');
        break;

      case 'checklist-runningTest':
        // Only wait up to 8 seconds for previews to load,
        // otherwise show individual loading spinners
        clearTimeout(this.waitForPreviewsTimeout);
        this.waitForPreviewsTimeout = setTimeout(this.stopWaitingForFirstPreviews.bind(this), 8000);

        // Reset error when running a new test
        this.setState({
          retestEnabled: false,
          errorType: null
        });

        chrome.runtime.sendMessage({ type: 'test_lifecycle', data: { status: 'running' } });

        this.recordInSegment('run_test');
        break;

      case 'checklist-testResult':
        this.onChecklistTestResult(message);
        break;

      case 'checklist-testError':
        this.setState({
          retestEnabled: false,
          errorType: message.data.error || message.data.result
        });

        this.recordInSegment('test_error');
        break;

      case 'checklist-instantAPIResult':
        this.onInstantApiResult(message);
        break;

      case 'checklist-makeScreenActive':
        this.setState({retesting: false});
        break;

      case 'checklist-show-mfa-setup':
        Litmus.goToPage('mfa-setup');

        this.recordInSegment('open_mfa_setup');
        break;

      case 'checklist-create-mfa':
        authToken = this.state.accounts[this.state.currentAccount].token;
        mfaData   = message.data.data;

        let mfaSuccess = (data) => {
          window.postMessage({ method: 'checklist-show-new-second-factor', newCode: true }, '*');
        };
        let mfaFailure = (xhr, ajaxOptions, thrownError) => {
          this.setState({ mfaError: 'The phone number you entered was invalid. Please try again.' });
        };

        Litmus.createMfa(authToken, mfaData, mfaSuccess, mfaFailure);

        this.recordInSegment('create_mfa_setup');
        break;

      case 'checklist-show-new-second-factor':
        token = this.state.accounts[this.state.currentAccount].token;

        let tokenRequestSuccess = (data) => {
          // hooray!! do nothing for now, we're already showing the form
        };
        let tokenRequestFailure = (xhr, ajaxOptions, thrownError) => {
          // We'll probably want to notify the user / activate a resend button here
          console.log('failure fetching token.');
        };

        // show success message if this is the first time this is applied
        if (message.data.newCode) {
          this.setState({mfaNewCode: true });
        }

        Litmus.requestSmsToken(token, tokenRequestSuccess, tokenRequestFailure);
        Litmus.goToPage('new-second-factor');

        this.recordInSegment('open_new_second_factor');
        break;

      case 'checklist-second-factor-verify':
        authToken = this.state.accounts[this.state.currentAccount].token;
        mfaData   = message.data.data;

        if (!mfaData) {
          this.setState({ mfaError: 'Token cannot be blank.' });
          return;
        }

        let tokenVerifySuccess = (data) => {
          window.parent.postMessage({ method: 'checklist-persistMfa', token: data.data.authed_token }, '*');
          window.parent.postMessage({ method: 'checklist-runTest' }, '*');
          this.setState({
            mfaError: '',
            mfaSuccess: true
          });

          // display success message for a sec, then show checklist
          setTimeout(() => {
            Litmus.goToPage('checklist');
          }, 3000);
        };

        let tokenVerifyFailure = (xhr, ajaxOptions, thrownError) => {
          this.setState({ mfaError: 'The token you entered was invalid. Please try again.' });
        };

        Litmus.validateMfaToken(authToken, mfaData, tokenVerifySuccess, tokenVerifyFailure);
        break;

      case 'checklist-timeoutError':
        this.setState({
          retestEnabled: false,
          errorType: 'timeout'
        });

        this.recordInSegment('test_timeout');
        break;

      case 'checklist-showRetestButton':
        // Only show retest button if we have a test result (and we're not already showing it)
        if (this.state.testResult && !this.state.retestEnabled) {
          this.setState({
            retestEnabled: true
          });
          this.recordInSegment('show_retest_prompt');
        }
        break;

      case 'checklist-hideRetestButton':
        // Only show retest button if we have a test result (and we're not already hiding it)
        if (this.state.testResult && this.state.retestEnabled) {
          this.setState({
            retestEnabled: false
          });
          this.recordInSegment('hide_retest_prompt');
        }
        break;

      case 'checklist-show-login':
        Litmus.goToPage('login');
        this.setState({mode: 'login'});

        this.recordInSegment('open_login');
        break;

      case 'checklist-show-dashboard':
        Litmus.goToPage('dashboard');
        this.setState({mode: 'dashboard'});

        this.recordInSegment('open_dashboard');
        break;

      case 'checklist-show-read-only-cta':
        Litmus.goToPage('readOnlyCta');
        this.setState({mode: 'readOnlyCta'});

        this.recordInSegment('open_read-only-cta');
        break;

      case 'checklist-show-checklist':
        Litmus.goToPage('checklist');
        this.setState({mode: 'checklist'});

        this.recordInSegment('open_checklist');
        break;

      case 'checklist-show-enterprise-cta':
        Litmus.goToPage('enterpriseCta');
        this.setState({mode: 'enterpriseCta'});

        this.recordInSegment('open_enterprise-cta');
        break;

      case 'checklist-show-extension-upgrade-cta':
        Litmus.goToPage('extensionUpgradeCta');
        this.setState({mode: 'extensionUpgradeCta'});

        this.recordInSegment('open_extension-upgrade-cta');
        break;

      case 'checklist-show-esp-upgrade-cta':
        Litmus.goToPage('espUpgradeCta');
        this.setState({mode: 'espUpgradeCta'});

        this.recordInSegment('open_esp-upgrade-cta');
        break;

      case 'checklist-show-over-usage-limit-cta':
        Litmus.goToPage('overUsageCta');
        this.setState({mode: 'overUsageCta'});

        this.recordInSegment('open_over-usage-limit-cta');
        break;

      case 'checklist-show-logged-out-upsell':
        Litmus.goToPage('loggedOutUpsell');
        this.setState({mode: 'loggedOutUpsell'});

        this.recordInSegment('open_logged-out-upsell');
        break;

      case 'checklist-handle-unsupported-dashboard':
        if (this.state.loggedIn) {
          postMessage({method: 'checklist-show-dashboard' }, '*');
        } else {
          postMessage({method: 'checklist-show-logged-out-upsell' }, '*');
        }
        break;

      case 'checklist-replaceCurrentPreview':
        this.reloadPreviewImage(message.data.result, message.data.code);
        this.recordInSegment('replace_current_preview');

      case 'checklist-expandFullScreen':
        this.setState({expandedFullScreen: true});

        if (message.data.persist) {
          this.setState({fullScreenPreference: true});
        }

        this.recordInSegment('expand_fullscreen');
        break;

      case 'checklist-collapseFullScreen':
        this.setState({expandedFullScreen: false});

        if (message.data.persist) {
          this.setState({fullScreenPreference: false});
        }
        break;

      case 'checklist-activateWideSidebar':
        this.setState({wide: true});
        break;

      case 'checklist-deactivateWideSidebar':
        this.setState({wide: false});
        break;

      case 'checklist-pusherEmailCapture':
        this.handlePusherEmailCaptureEvent(message.data.captures);
        break;

      case 'checklist-skipFirstImpressionsSection':
        this.setState({
          skipFirstImpressionsSection: true
        });
        break;

      case 'checklist-recordPageAction':
        this.recordPageAction(message.data.action, message.data.attributes);
        break;

      case 'checklist-saveEmailId':
        this.setState({
          espTestId: message.data.id
        });
        break;

      case 'logout':
        let success = (data) => {
          chrome.storage.local.remove(['current_account', 'accounts'], () => {
            this.setState({ currentAccount: null });
            Litmus.removeMfaToken();
          });
        };
        let failure   = (xhr, ajaxOptions, thrownError) => {
          console.log('Logout Error!');
          console.log(thrownError);
        };
        Litmus.logout(success, failure);
        break;
    }
  }

  handlePusherEmailCaptureEvent(captures) {
    if (!this.state.testResult) {
      return;
    }

    const newTestResult = {...this.state.testResult};

    let newCaptures = false;

    // Iterate through all captures, try to match capture codes with previews
    captures.forEach((capture) => {
      const captureCode = capture.Client.toLowerCase();

      this.state.testResult.previews.some((preview, previewIndex) => {
        if (captureCode === preview.code) {
          // Don't trigger a state change if preview image was already ready
          if (!preview.ready && !preview.failed) {
            if (capture.State === 'Failed') {
              newTestResult.previews[previewIndex].failed = true;
            } else if (capture.State === 'Completed') {
              newTestResult.previews[previewIndex].ready = true;

              if (previewIndex < 4) {
                this.preloadPreview(previewIndex);
              }
            }

            newCaptures = true;
          }
          return true;
        }
      });
    });

    // Only update the state if there is a new capture
    if (newCaptures) {
      this.setState({
        testResult: newTestResult
      });
    }
  }

  onChecklistTestResult(message) {
    let testResult, emailId, folderId;
    let payload    = message.data.result;

    if (payload.checklist) {
      testResult = payload.checklist;
      emailId    = payload.id;
      folderId   = payload.folder_id;
    } else {
      testResult = payload;
    }

    // Sort previews to show unrestricted previews first
    let restrictedPreviews = [];
    let unrestrictedPreviews = [];
    var trial = testResult.account.trial_plan;
    var previewsCount = testResult.previews_count;
    var totalLimit = testResult.total_previews_limit;
    var hasAccess  = testResult.account.has_access;
    var planGroup  = testResult.account.plan_group;
    let admin = testResult.user ? testResult.user.admin : null;
    let testId     = testResult.id || testResult.guid;

    // set total client count before filtering
    testResult.totalClientCount = testResult.previews.length;

    testResult.previews.filter((preview) => preview.selected).forEach(function(p) {
      p.restricted ? restrictedPreviews.push(p) : unrestrictedPreviews.push(p);
    });
    testResult.previews = unrestrictedPreviews.concat(restrictedPreviews);

    this.unapproveChangedSections(testResult);

    this.setState({
      testResult: testResult,
      freeAccount: testResult.account && testResult.account.free_plan,
      espUri: message.data.espUri,
      firstPreviewsReady: false,
      lastRunDate: new Date(),
      lastInstantApiDate: null,
      reportId: message.data.result.email_id,
      retestEnabled: false,
      errorType: null,
      showAllPreviews: (this.state.currentEsp.name === '' || typeof this.state.currentEsp.name === 'undefined'),
      waitingForFirstPreviews: false,
      checklistScrollY: 0,
      retesting: false,
      trial: trial,
      hasAccess: hasAccess,
      planGroup: planGroup,
      previewsCount: previewsCount,
      totalLimit: totalLimit,
      admin: admin,
      testId: testId,
      emailId: emailId,
      folderId: folderId,
      local: (this.state.currentEsp.name === '' || typeof this.state.currentEsp.name === 'undefined')
    });

    // Request renders and subscribe to email_capture events for the current email
    window.parent.postMessage({
      method: 'checklist-requestScreenshots',
      testId: testId,
      pusherChannelName: testResult.channel_name
    }, '*');

    // If we have any restricted previews, preload the first one
    // (we don't get any pusher events for these, they need to be rendered manually)
    if (restrictedPreviews.length) {
      this.preloadPreview(3);
    }

    this.recordInSegment('test_complete');
  }

  onInstantApiResult(message) {
    if (this.state.currentEsp.name === '' || typeof this.state.currentEsp.name === 'undefined') {
      chrome.runtime.sendMessage({ type: 'test_lifecycle', data: { status: 'localActive' } });
    }

    // We just received a new email GUID from the Instant API,
    // so update all fullscreen screenshots with the new URL
    //let newTestResult = {...this.state.testResult};

    let newTestResult = message.data.result || this.state.testResult;
    let guid = newTestResult.email_guid || newTestResult.guid;
    newTestResult.totalClientCount = newTestResult.previews.length;
    let localRestrictedPreviews = [];
    let localUnrestrictedPreviews = [];
    var trial = newTestResult.account.trial_plan;
    var hasAccess = newTestResult.account.has_access;
    var planGroup = newTestResult.account.plan_group;
    var previewsCount = newTestResult.previews_count;
    var totalLimit = newTestResult.total_previews_limit;
    let admin = newTestResult.user ? newTestResult.user.admin : null;
    newTestResult.previews.filter((preview) => preview.selected).forEach(function(p) {
      p.restricted ? localRestrictedPreviews.push(p) : localUnrestrictedPreviews.push(p);
    });

    newTestResult.previews = localUnrestrictedPreviews.concat(localRestrictedPreviews);

    window.parent.postMessage({
      method: 'checklist-requestInstantScreenshots',
      pusherChannelName: newTestResult.channel_name,
      guid: guid
    }, '*');

    this.setState({
      retestEnabled: false,
      retesting: false,
      firstPreviewsReady: false,
      lastRunDate: new Date(),
      testResult: newTestResult,
      showAllPreviews: (this.state.currentEsp.name === '' || typeof this.state.currentEsp.name === 'undefined'),
      waitingForFirstPreviews: false,
      errorType: null,
      trial: trial,
      hasAccess: hasAccess,
      planGroup: planGroup,
      previewsCount: previewsCount,
      totalLimit: totalLimit,
      admin: admin,
      local: (this.state.currentEsp.name === '' || typeof this.state.currentEsp.name === 'undefined')
    });
    this.recordInSegment('test_complete');
  }

  reloadPreviewImage(result, code) {
    let preview = this.state.testResult.previews.filter((preview) => {
      return preview.code === code;
    })[0];

    // preload image
    let img = new Image();
    img.onload = () => {
      preview.screenshots.full = result.fullUrl;
      preview.screenshots.thumbnail = result.thumbUrl;

      this.setState({
        retesting: false,
        retestEnabled: false
      });

      this.refs.page.stopLoading();
    };
    img.src = `http://${result.fullUrl}`;
  }

  preloadPreview(index) {
    const preview = this.state.testResult.previews[index];
    let thumbnailURL = preview.screenshots.large_thumbnail.replace(/^\/\//, 'https://');

    const image = new Image();
    image.onload  = this.previewPreloadCompleted.bind(this, index, 'loaded');
    image.onerror = this.previewPreloadCompleted.bind(this, index, 'failed');
    image.src = thumbnailURL;
  }

  previewPreloadCompleted(index, status) {
    const newTestResult = {...this.state.testResult};

    // We might be running a retest, clearing the old testResult
    if (newTestResult === null) {
      return;
    }

    newTestResult.previews[index][status] = true;

    const firstPreviewsReady = this.checkFirstPreviewsReady(newTestResult);

    // If first previews are now ready
    if (!this.state.firstPreviewsReady && firstPreviewsReady) {
      // Tell the parent window that we're totally ready now,
      // which changes the extension icon, and slightly slides out the sidebar
      window.parent.postMessage({method: 'checklist-firstPreviewsReady'}, '*');

      // Don't need to wait for previews anymore
      clearTimeout(this.waitForPreviewsTimeout);

      this.setState({
        waitingForFirstPreviews: false
      });
    }

    this.setState({
      testResult: newTestResult,
      firstPreviewsReady: firstPreviewsReady
    });
  }

  checkFirstPreviewsReady(testResult) {
    let ready = false;
    if (testResult) {
      ready = testResult.previews.slice(0, 4).every((preview) => {
        return preview.restricted || preview.loaded || preview.failed;
      });
    }
    return ready;
  }

  stopWaitingForFirstPreviews() {
    this.setState({
      waitingForFirstPreviews: false
    });
  }

  isLocal() {
    return (this.state.parentUrl.includes('file:') ||
    this.state.parentUrl.includes('localhost') ||
    this.state.parentUrl.includes('127.0.0.1'));
  }

  setCalloutLink() {
    let slug = this.state.currentEsp && this.state.currentEsp.pardotSlug;
    let endpoint = this.state.loggedIn ? 'billing' : 'pricing';

    // account for local files
    if (this.isLocal()) {
      slug = 'localhtmleditor';
    }

    slug = slug || 'webpage';

    let link = `https://litmus.com/${endpoint}?utm_source=${slug}&utm_medium=litmusextension`;
    this.setState({ calloutLink: link });
    return link;
  }

  instantApiPreloadCompleted(previewIndex, newPreviewURL, status) {
    if (status === 'failed') {
      // console.log("Could not load Instant API preview! Continuing with broken image for now...");
    }

    // Our Instant API image has been preloaded, so now we finally
    // update this preview with the new full screenshot URL.
    // We also update the "lastInstantApiDate", which updates the Page key and
    // triggers a re-render of all child elements,
    // which resets the state of the preview page to undim the previews,
    // and also displays the new preview from the Instant API.
    let newTestResult = {...this.state.testResult};
    newTestResult.previews[previewIndex].screenshots.full = newPreviewURL;

    this.setState({
      lastInstantApiDate: new Date(),
      testResult: newTestResult
    });
  }

  recordPageAction(action_name, attributes = {}) {
    if (!Litmus.ENV.isProduction()) {
      return;
    }

    let newrelic = this.props.newrelic;
    let account  = (this.state.accounts && this.state.currentAccount !== null) ? this.state.accounts[this.state.currentAccount] : null;

    let defaultAttrs = {
      current_esp: this.state.currentEsp.name,
      tab_url: this.state.parentUrl,
      email_code: `${this.state.currentEsp.code}-${this.state.espTestId}`,
      number_of_previews: this.state.testResult ? this.state.testResult.previews.length : null,
      logged_in: this.state.loggedIn,
      current_account: account && account.account_id,
      current_user: account && account.user_id,
      enterprise_plan: account && account.enterprise,
      extension_enabled: account && account.extension_enabled,
      user_read_only: account && account.user_read_only
    };

    attributes = Object.assign(defaultAttrs, attributes);

    newrelic.addPageAction(action_name, attributes);
    this.recordInSegment(action_name, attributes);
  }

  recordInSegment(event_name, extraInfo = null) {
    var currentState = this.state;
    let account  = (this.state.accounts && this.state.currentAccount !== null) ? this.state.accounts[this.state.currentAccount] : null;
    let payload = {
      analyticsEventType: "track",
      eventData: {
        event: event_name,
        userId: (account && account.user_id) ? `user_${account.user_id}` : '',
        properties: Object.assign({
          current_esp: currentState.currentEsp.name,
          tab_url: currentState.espUri,
          checklist_id: currentState.testResult ? currentState.testResult.id : null,
          number_of_previews: currentState.testResult ? currentState.testResult.previews.length : null,
          logged_in: currentState.loggedIn,
          free_plan: currentState.freeAccount,
          current_url: currentState.parentUrl,
          file_access: currentState.fileAccess
        }, extraInfo),
    }};
    chrome.runtime.sendMessage({ type: "send_analytics", data: payload});
  }
}



