import { Component } from 'react';

export default class LitmusCTA extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        <p>To check and test your emails using Litmus you will need an account. Sign in, or start a free Litmus trial.</p>
        <a className="btn btn-secondary btn-lg btn-block" href="https://litmus.com/hello" target="_blank"><span className="button-text">Try Litmus free</span></a>
      </div>
    );
  }
}



