var React = require('react');
import { Component } from 'react';
import PropTypes from 'prop-types';
const Litmus  = require('../../../base/litmus');

export default class AuthenticatedMenu extends Component {

  static get propTypes() {
    return {
      redirectUrl: PropTypes.string,
      admin: PropTypes.bool,
      recordInSegment: PropTypes.func
    };
  }

  onClickLogout(e) {
    e.preventDefault();
    window.parent.postMessage({ method: 'checklist-collapseFullScreen' }, '*');
    window.postMessage({ method: 'logout' }, '*');
    Litmus.goToPage(this.props.redirectUrl);
  }

  onClickSwitchAccounts(e) {
    e.preventDefault();
    Litmus.goToPage('accounts');
  }

  onClickSettings(e) {
    e.preventDefault();
    window.open('https://litmus.com/settings?utm_medium=litmusextension', '_blank');
    this.props.recordInSegment('Account Settings clicked');
  }

  onClickClients(e) {
    e.preventDefault();
    window.open('https://litmus.com/settings/clients?utm_medium=litmusextension', '_blank');
    this.props.recordInSegment('Configure email clients clicked');
  }

  onClickUpgrade(e) {
    e.preventDefault();
    window.open('https://litmus.com/billing?utm_medium=litmusextension', '_blank');
    this.props.recordInSegment('Billing link clicked');
  }

  render() {
    let addUsers = this.props.admin ? <a className="menu-item add-users" href="https://litmus.com/users" target="_blank">Add Users</a> : '';

    return (
      <div className="settings-menu hover-menu">
        <a className="menu-item switch-accounts" href="#" onClick={this.onClickSwitchAccounts.bind(this)}>
          Switch Litmus accounts
        </a>
        <a className="menu-item configure-clients" href="#" target="_blank" onClick={this.onClickClients.bind(this)}>
          Configure email clients
        </a>

        {addUsers}

        <a className="menu-item user-settings" href="#" onClick={this.onClickSettings.bind(this)}>
          Account settings
        </a>
        <a className="menu-item upgrade-plan" href="#" onClick={this.onClickUpgrade.bind(this)}>
          Upgrade your plan
        </a>

        <a className="menu-item log-out" href="#" onClick={this.onClickLogout.bind(this)}>
          Log out
        </a>
      </div>
    );
  }
}




