import { Component } from 'react';
import PropTypes from 'prop-types';


export default class ActivationCTA extends Component {

	render(){
    let ctaUrl = null;
    if (this.props.userType === 'nonUser') {
        ctaUrl = 'https://litmus.com/pricing?utm_medium=litmusextension';
    } else {
        ctaUrl = 'https://litmus.com/billing?utm_medium=litmusextension';
    }
		return (
			<div className="panel section_cta">
				<p> {this.props.activationMessage} </p>
				<div className="panel-content">
					<a className="btn primary cta_button" href={ctaUrl} target="_blank"> Activate my account </a>
				</div>
			</div>
		);
	}
}

ActivationCTA.propTypes = {
  userType: PropTypes.string.isRequired,
  activationMessage: PropTypes.string.isRequired
};



