import { Component } from 'react';
import PropTypes from 'prop-types';

export default class ContentFullPreview extends Component {

  static propTypes = {
    preview: PropTypes.object,
    retesting: PropTypes.bool
  };

  constructor() {
    super();
    this.state = {
      showSpinner: true
    };
  }

  _handleImageLoaded() {
    this.setState({showSpinner: false});
  }

	render() {
		const preview = this.props.preview;
		const imageUrl = preview.url;

    let Spinner = this.state.showSpinner
                  ? <div className="spinner-container">
                      <div className="image-spinner"></div>
                    </div> : '';

		return (
			<div className="full-preview">
				<div className="screenshot-container">
					<div className="spinner-container">
            {
              Spinner
            }
						<img
							className={'animated fadeIn screenshot' + (this.props.retesting ? ' dim' : '')}
							key={imageUrl}
							src={imageUrl}
              onLoad={this._handleImageLoaded.bind(this)}
							title="Email Screenshot" />
					</div>
				</div>
			</div>
		);
	}
}



