import { Component } from 'react';
import PropTypes from 'prop-types';

import Header from '../../components/header';
import SectionsContainer from './components/sectionsContainer';
import RetestBanner from '../../components/retestBanner';
import UrlFooter from '../../components/urlFooter';
import Litmus from '../../../../base/litmus';

export default class Checklist extends Component {

  static propTypes = {
    testResult: PropTypes.object,
    firstPreviewsReady: PropTypes.bool,
    waitingForFirstPreviews: PropTypes.bool,
    errorType: PropTypes.object,
    skipFirstImpressions: PropTypes.bool,
    approvedSections: PropTypes.object,
    toggleApproved: PropTypes.func,
    recordInSegment: PropTypes.func,
    loggedIn: PropTypes.bool,
    currentEsp: PropTypes.object,
    showAllPreviews: PropTypes.bool,
    onClickShowAllPreviews: PropTypes.func,
    expandedFullScreen: PropTypes.bool,
    accounts: PropTypes.arrayOf(PropTypes.object),
    currentAccount: PropTypes.number,
    onClickRetest: PropTypes.func,
    mode: PropTypes.string,
    wide: PropTypes.bool,
    retestEnabled: PropTypes.bool,
    skipFirstImpressionsSection: PropTypes.bool,
    local: PropTypes.bool,
    trial: PropTypes.bool,
    testUrl: PropTypes.string,
    hasAccess: PropTypes.bool.isRequired,
    previewsCount: PropTypes.number,
    totalLimit: PropTypes.number,
    testId: PropTypes.number,
    folderId: PropTypes.number,
    emailId: PropTypes.number,
    admin: PropTypes.bool,
    planGroup: PropTypes.string,
    enterprise: PropTypes.bool,
    calloutLink: PropTypes.string
  };

  constructor(props) {
    super(props);
    this.onClickUrl = this.onClickUrl.bind(this);
  }

  render() {
    let content = '';

    const showSections = (
      this.props.testResult && (
        this.props.firstPreviewsReady || !this.props.waitingForFirstPreviews
      )
    );

    const showError = this.props.errorType;

    const headerRetestEnabled = !!(showError || showSections);

    if (showError) {
      content = this.renderError();
    } else if (showSections) {
      content = this.renderSections();
    } else {
      content = this.renderLoader();
    }

    let urlFooter = this.props.testId && !this.props.local && headerRetestEnabled ? <UrlFooter onClickUrl={this.onClickUrl} /> : '';
    let currentAccount = this.props.loggedIn && this.props.accounts[this.props.currentAccount];

    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          onClickRetest={this.props.onClickRetest}
          recordInSegment={this.props.recordInSegment}
          retestEnabled={headerRetestEnabled}
          mode={this.props.mode}
          admin={this.props.admin}
          testResult={this.props.testResult}
        />
        
        <RetestBanner
          onClickRetest={this.props.onClickRetest}
          currentEsp={this.props.currentEsp}
          visible={this.props.retestEnabled}
        />

        { content }

        { urlFooter }
      </div>
    );
  }

  renderLoader() {
    return (
      <div id="waiting">
        <div className="helper">
          <div className="loader">
            <div className="spinner"></div>
          </div>
        </div>
      </div>
    );
  }

  renderError() {
    return (
      <div id="error">
        <div className="error-content">
          <img src="/images/error.png" alt="Error" />
          <h3>Uh oh! We had a problem running your test.</h3>
          <p>
            Appears we had a problem running our email through the checklist.&nbsp;
            Please try <a href="#" className="retest" onClick={this.props.onClickRetest}>re-testing</a> your email, or email <a href="mailto:hello@litmus.com">hello@litmus.com</a> for assistance.
          </p>
        </div>
      </div>
    );
  }

  isSkippedFirstImpressionSection(sectionName) {
    if (this.props.skipFirstImpressionsSection) {
      return true;
    } else {
      return this.props.currentEsp ? false : true;
    }
  }

  renderSections() {
    let skipFirstImpressions = this.isSkippedFirstImpressionSection();
    let currentAccount = this.props.loggedIn && this.props.accounts[this.props.currentAccount];

    return (
      <SectionsContainer
      	skipFirstImpressions={
      		skipFirstImpressions
      	}
      	loggedIn={this.props.loggedIn}
        approvedSections={this.props.approvedSections}
        toggleApproved={this.props.toggleApproved}
        testResult={this.props.testResult}
        expandedFullScreen={this.props.expandedFullScreen}
        showAllPreviews={this.props.showAllPreviews}
        onClickShowAllPreviews={this.props.onClickShowAllPreviews}
        recordInSegment={this.props.recordInSegment}
        wide={this.props.wide}
        trial={this.props.trial}
        hasAccess={this.props.hasAccess}
        previewsCount={this.props.previewsCount}
        totalLimit={this.props.totalLimit}
        toggleApproved={this.props.toggleApproved.bind(this, 'images')}
        imagesResult={this.props.testResult.images}
        visible={!this.props.local}
        enterprise={this.props.enterprise}
        calloutLink={this.props.calloutLink}
        onClickOpenInLitmus={this.onClickUrl.bind(this)}
        local={(this.props.currentEsp && (this.props.currentEsp.name === '' || typeof this.props.currentEsp.name === 'undefined'))}
       />
    );
  }

  onClickUrl(e) {
    e.preventDefault();

    if (this.props.folderId && this.props.emailId) {
      window.open(Litmus.getCatalystChecklistLink(this.props.folderId, this.props.emailId));
    } else {
      window.open(Litmus.getTestLink(this.props.testId));
    }

    this.props.recordInSegment('View in Litmus button clicked');
  }
}

// go straight to detail if it was last opened
if (sessionStorage.currentHash) {
  Litmus.goToPage(sessionStorage.currentHash);
}



