import {Component } from 'react';
import PropTypes from 'prop-types';
import StickyHeaders from '../../../components/stickyHeaders';

import SummarySection from './sections/summary';
import PreviewsSection from './sections/previews';
import FirstImpressionsSection from './sections/firstImpressions';
import ContentSection from './sections/content';
import LinksSection from './sections/links';
import ImagesSection from './sections/images';
import TrackingSection from './sections/tracking';
import WhatsNextSection from './sections/whatsNext';
import calculateIssuesCount from '../../../../issueCount';

export default class SectionsContainer extends Component {

  static propTypes = {
    testResult: PropTypes.object,
    skipFirstImpressions: PropTypes.bool,
    approvedSections: PropTypes.object,
    toggleApproved: PropTypes.func,
    loggedIn: PropTypes.bool,
    local: PropTypes.bool,
    showAllPreviews: PropTypes.bool,
    onClickShowAllPreviews: PropTypes.func,
    onClickOpenInLitmus: PropTypes.func,
    recordInSegment: PropTypes.func,
    expandedFullScreen: PropTypes.bool,
    wide: PropTypes.bool,
    trial: PropTypes.bool.isRequired,
    hasAccess: PropTypes.bool.isRequired,
    previewsCount: PropTypes.number.isRequired,
    totalLimit: PropTypes.number.isRequired,
    enterprise: PropTypes.bool,
    calloutLink: PropTypes.string
  };

  render() {
    // skip failed previews
    const selectedPreviews     = this.props.testResult.previews.filter((preview) => (!preview.failed));
    const userType             = this.getCurrentUserType();
    const issueCounts          = calculateIssuesCount(this.props.testResult);

    let firstSectionWithIssue = 'summary';
    let sectionsInOrder = ['firstImpressions', 'content', 'links', 'tracking', 'images'];
    for (let i = 0; i < sectionsInOrder.length; i++) {
      if (issueCounts[sectionsInOrder[i]].failed > 0) {
        firstSectionWithIssue = sectionsInOrder[i];
        break;
      }
    }

    // no first impressions for local files
    let firstImpressionsSection = null;
    if (!this.props.skipFirstImpressions && !this.props.local){
		firstImpressionsSection = <FirstImpressionsSection
						          userType={userType}
						          onHeaderUpdate={this.componentDidUpdate.bind(this)}
						          issuesCount={issueCounts['firstImpressions']['failed']}
						          approved={this.props.approvedSections.firstImpressions}
						          toggleApproved={this.props.toggleApproved.bind(this, 'firstImpressions')}
						          subjectLine={this.props.testResult.subject_line}
						          from={this.props.testResult.from}
						          replyTo={this.props.testResult.reply_to}
						          previewText={this.props.testResult.preview_text}
						        />;
    }

    let testResult = this.props.testResult || { tracking: { links: [] }, links: { link_checks: [] } };
    // always show tracking if we have results.  If not, hide if user is in beta
    let trackedLinks = ((testResult.tracking && testResult.tracking.links) || []).filter((l) => l.campaign || l.medium || l.source);
    let showTrackingLinks = (trackedLinks.length > 0);
    let showLinkChecks = (testResult.links && testResult.links.link_checks.length > 0);

    return (
      <div className="sections">
        <SummarySection
          firstSectionWithIssue={firstSectionWithIssue}
          skipFirstImpressions={this.props.skipFirstImpressions}
          onHeaderUpdate={this.componentDidUpdate.bind(this)}
          approved={this.props.approvedSections.firstImpressions}
          toggleApproved={this.props.toggleApproved.bind(this, 'firstImpressions')}
          totalIssueCounts={issueCounts['total']}
          visible={!this.props.local}
        />
        <PreviewsSection
          skipFirstImpressions={this.props.skipFirstImpressions}
          userType={userType}
          loggedIn={this.props.loggedIn /* required for redirect flow */}
          onHeaderUpdate={this.componentDidUpdate.bind(this)}
          recordInSegment={this.props.recordInSegment}
          approved={this.props.approvedSections.previews}
          toggleApproved={this.props.toggleApproved.bind(this, 'previews')}
          previews={selectedPreviews}
          showAllPreviews={this.props.showAllPreviews}
          local={this.props.local}
          wide={this.props.wide}
          totalClientCount={this.props.testResult.totalClientCount}
          trial={this.props.trial}
          hasAccess={this.props.hasAccess}
          previewsCount={this.props.previewsCount}
          totalLimit={this.props.totalLimit}
          calloutLink={this.props.calloutLink}
          onClickShowAllPreviews={this.props.onClickShowAllPreviews}
        />
        { firstImpressionsSection }
        <LinksSection
          skipFirstImpressions={this.props.skipFirstImpressions}
          userType={userType}
          onHeaderUpdate={this.componentDidUpdate.bind(this)}
          issuesCount={issueCounts['links']['failed']}
          approved={this.props.approvedSections.links}
          toggleApproved={this.props.toggleApproved.bind(this, 'links')}
          link_checks={this.props.testResult.links.link_checks}
          visible={!this.props.local && showLinkChecks}
        />

        <TrackingSection
          skipFirstImpressions={this.props.skipFirstImpressions}
          userType={userType}
          onHeaderUpdate={this.componentDidUpdate.bind(this)}
          issuesCount={issueCounts['tracking']['failed']}
          approved={this.props.approvedSections.tracking}
          toggleApproved={this.props.toggleApproved.bind(this, 'tracking')}
          links={this.props.testResult.tracking.links}
          visible={!this.props.local && showTrackingLinks}
        />

        <ContentSection
          skipFirstImpressions={this.props.skipFirstImpressions}
          userType={userType}
          onHeaderUpdate={this.componentDidUpdate.bind(this)}
          issuesCount={issueCounts['content']['failed']}
          approved={this.props.approvedSections.content}
          toggleApproved={this.props.toggleApproved.bind(this, 'content')}
          previewUrl={this.props.testResult.content.images_off_preview_url}
          imageTags={this.props.testResult.content.image_tags}
          visible={!this.props.local}
        />
        <ImagesSection
          skipFirstImpressions={this.props.skipFirstImpressions}
          userType={userType}
          onHeaderUpdate={this.componentDidUpdate.bind(this)}
          issuesCount={issueCounts['images']['failed']}
          approved={this.props.approvedSections.images}
          toggleApproved={this.props.toggleApproved.bind(this, 'images')}
          imagesResult={this.props.testResult.images}
          visible={!this.props.local}
        />

        { this.props.local ?
          null
          :
          <WhatsNextSection
            onClickOpenInLitmus={this.props.onClickOpenInLitmus}
          />
        }
      </div>
    );
  }

  getCurrentUserType() {
    let userType = null;
    if (this.props.loggedIn && this.props.testResult.account ) {
      if (this.props.testResult.account.free_plan) {
        userType = 'freeUser';
      } else {
        userType = 'paidUser';
      }
    } else {
      userType = 'nonUser';
    }
    return userType;
  }

  componentDidMount() {
    StickyHeaders.load();

    // If the sidebar was previously expanded full screen, then
    // we are currently running a 0.2 second animation to collapse back down
    // to the sidebar. We need to reload the Stickyheaders initial position
    // once that animation has finished.
    if (this.props.expandedFullScreen) {
      window.setTimeout(function() {
        StickyHeaders.load();
      }, 250);
    }
  }

  componentDidUpdate() {
    // Have to wait for DOM to be updated
    window.setTimeout(function() {
      StickyHeaders.load();
    }, 25);
  }

  componentWillUnmount() {
    StickyHeaders.unload();
  }
}



