import PropTypes from 'prop-types';

import BaseSection from '../baseSection';

export default class SummarySection extends BaseSection {
  static sectionName = 'Report Summary';
  static sectionSlug = 'summary';

  static propTypes = {
    totalIssueCounts: PropTypes.object
  }

  onReviewClick(slug, e) {
    e.preventDefault();
    e.stopPropagation();

    const scrollTo = $(`.section.${slug}`);
    $('body').animate({
      scrollTop: scrollTo.position().top - 60
    }, 500, 'swing');
  }

  renderHeader() {
    return;
  }

  renderSectionBody() {
    const passedPercentage = Math.round(((
      this.props.totalIssueCounts['passed'] /
      this.props.totalIssueCounts['total']
    ) * 100));

    const issueCount = this.props.totalIssueCounts['failed'];

    return (
      <div className="section-body">
        { issueCount ? 
        <div className="outstanding-issues">
          <div className="outstanding-issues-icon">
            <span className="icon-warning"></span>
          </div> 
          <div className="outstanding-issues-description">
            There { issueCount === 1 ? 'is' : 'are' } <b>{issueCount}</b> outstanding issue{ issueCount === 1 ? '' : 's' }.
          </div>
        </div> : '' }
      </div>
    );
  }
}



