import React from 'react';
import PropTypes from 'prop-types';
import { Component } from 'react';
import Header from '../../components/header';
import ESPSelectionList from './components/espSelectionList';
import ESPSelectItem from './components/espSelectItem';
import AuthenticatedESPSelection from './components/authenticatedESPSection';
import AuthenticatedESP from './components/authenticatedESP';
import LitmusCTA from './components/litmusCTA';
import HostedTestOptions from './components/hostedTestOptions';
import providers from '../../../../base/providers.json';


export default class DashboardPage extends Component {

  static get propTypes() {
    return {
      currentAccount: PropTypes.number,
      accounts: PropTypes.arrayOf(PropTypes.object),
      mode: PropTypes.string,
      calloutLink: PropTypes.string,
      enterprise: PropTypes.bool,
      recordInSegment: PropTypes.func
    }
  }

  onCalloutClick(e) {
    e.preventDefault();

    window.open(this.props.calloutLink && (this.props.calloutLink + '#enterprise'), '_blank');
    this.props.recordInSegment('Billing link clicked');
  }

  constructor(props) {
    super(props);
    // this.supportedESPs = Object.keys(providers).map( key => providers[key] ).filter( esp => esp.active == true);
    this.supportedESPs = [
      {
        name: "MailChimp",
        login_url: "https://login.mailchimp.com/",
        img_url: "/images/esps/mailchimp.svg",
        active: true
      },
      {
        name: "Campaign Monitor",
        login_url: "https://login.createsend.com/l",
        img_url: "/images/esps/campaign_monitor.svg",
        active: true
      },
      {
        name: "Eloqua",
        login_url: "https://login.eloqua.com/l",
        img_url: "/images/esps/eloqua.svg",
        active: false
      },
      {
        name: "Marketo",
        login_url: "https://login.marketo.com",
        img_url: "/images/esps/marketo.svg",
        active: false
      },
      {
        name: "Acoustic Campaign",
        login_url: "https://engage3.silverpop.com",
        img_url: "/images/esps/acoustic.svg",
        active: false
      },
      {
        name: "Salesforce Marketing Cloud (formerly ExactTarget)",
        login_url: "https://auth.s1.exacttarget.com",
        img_url: "/images/esps/exact_target.svg",
        active: false
      }
    ];

    this.state = {
      esp_accounts: []
    };
  }

  renderGuestESPSection(espAccounts) {
    let output = [];
    let plusEsps = this.supportedESPs.filter((esp) => { return esp.active; } );
    let enterpriseEsps = this.supportedESPs.filter((esp) => { return !esp.active; });
    let account = this.props.accounts && this.props.accounts[this.props.currentAccount];
    let contact = account && account.account_contact;

    if (this.props.enterprise) {
      plusEsps.map( (esp, i) => {
        output.push(<ESPSelectItem {...esp} key={i} upgrade_url={this.props.calloutLink} recordInSegment={this.props.recordInSegment} />);
      });
    } else {
      plusEsps.map((esp, i) => {
        output.push(<ESPSelectItem {...esp} key={`${i}-plus`} upgrade_url={this.props.calloutLink} recordInSegment={this.props.recordInSegment} />);
      });
      output.push(<p key="available-enterprise" className="available-enterprise">Available exclusively on Litmus Enterprise. <br /><a href="#" onClick={this.onCalloutClick.bind(this)}>Contact us to learn more</a></p>);
      enterpriseEsps.map((esp, i) => {
        output.push(<ESPSelectItem {...esp} key={`${i}-enterprise`} upgrade_url={this.props.calloutLink && (this.props.calloutLink + '#enterprise')} recordInSegment={this.props.recordInSegment} />);
      });
    }

    return (
      <ESPSelectionList esp_accounts={espAccounts} initial={true}>
        {
          output
        }
      </ESPSelectionList>
    );
  }

  renderAuthenticatedESPSection(espAccounts) {
    return (
      <AuthenticatedESPSelection esp_accounts={espAccounts} supportedESPs={this.supportedESPs} calloutLink={this.props.calloutLink} recordInSegment={this.props.recordInSegment}>
        {
          espAccounts.map((acc) => {
            let currentESP = this.supportedESPs.filter(esp => { return esp.name == acc.name })[0];
            let imgUrl = (currentESP && currentESP['img_url']) || "";
            return <AuthenticatedESP {...acc} key={acc.id} imgUrl={imgUrl} removeHandler={this.handleESPRemove.bind(this, acc.id)}/>;
          })
        }
      </AuthenticatedESPSelection>
    );
  }

  renderEnterpriseEspDescription() {
    let account = this.props.accounts && this.props.accounts[this.props.currentAccount];

    if (this.props.enterprise) {
      return(
        <div>
          <p>To instantly preview your campaign in popular email clients and see elements of pre-send Litmus Tests directly in Acoustic Campaign, Adobe Campaign Standard, Eloqua, Klaviyo, Marketo, Pardot, Responsys, or Salesforce Marketing Cloud (formerly ExactTarget) simply log in to your ESP account, open your email draft, and activate the Litmus Extension.</p>
          <ul className="esp-description-logos">
            <li><img src="/images/esps/acoustic.svg" alt="Acoustic Campaign logo" /></li>
            <li><img src="/images/esps/adobe-campaign.svg" alt="Adobe Campaign Standard logo" /></li>
            <li><img src="/images/esps/eloqua.svg" alt="Eloqua logo" /></li>
            <li><img src="/images/esps/marketo.svg" alt="Marketo logo" /></li>
            <li><img src="/images/esps/responsys.svg" alt="Responsys logo" /></li>
            <li><img src="/images/esps/exact_target.svg" alt="Salesforce Marketing Cloud logo" /></li>
          </ul>
          <hr />
        </div>
      );
    }
  }

  render() {
    let doneList = [];
    let espAccounts = this.state.esp_accounts;

    let espSection = null;
    if (this.state.esp_accounts.length > 0) {
      espSection = this.renderAuthenticatedESPSection(espAccounts);
    } else {
      espSection = this.renderGuestESPSection(espAccounts) ;
    }
    let enterpriseEsps = this.renderEnterpriseEspDescription();
    let testSection = null;
    let litmusSection = null;
    if (this.props.currentAccount !== null) {
      testSection = <HostedTestOptions handleHostedTest={this.handleHostedTest.bind(this)}/>
    } else {
      litmusSection = <LitmusCTA />
    }

    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          mode={this.props.mode}
        />
        <div className="sections">
          <div className="section dashboard expanded">
            <div className="section-body">
              <div className="extension-heading">
                <h2>Litmus Extension</h2>

                <p>Test with Litmus wherever you build</p>

                <hr />
              </div>

              {
                testSection
              }

              <br />

              <h3>Run a Litmus Test directly in your email service provider</h3>

              <p>By authenticating your email service provider (ESP), you can automatically run a Litmus Test every time you visit a drafted email in your ESP. <a href="https://litmus.com/help/testing/litmus-extension-guide?utm_medium=litmusextension#esp-support" target="_blank">Learn more</a>.</p>

              <p style={ { float: 'left' } }>Manage your ESPs:</p>
              <small><a href="https://litmus.com/help/testing/litmus-extension-guide?utm_medium=litmusextension#esp-support" target="_blank" style={ { cursor: 'pointer', float: 'right', 'marginRight': '10px'} }>Dont see your ESP listed?</a></small>
              {
                espSection
              }

              <hr />

              {
                enterpriseEsps
              }

              <h3>Run Litmus Email Previews directly on your desktop</h3>

              <p>If you build your emails in a local editor or task runner you can simply open your local HTML file in Chrome and activate the Litmus Extension to generate previews of your email in popular email clients.</p>

              <hr />

              {
                litmusSection
              }
            </div>
          </div>
        </div>
      </div>
    );
  }

  handleESPRemove(id) {
    chrome.runtime.sendMessage({
        type: 'storage_get',
        data: ['esp_accounts']
      },
      ((items) => {
        let newList = [];
        newList = items['esp_accounts'].filter( esp => {return esp.id != id });
        chrome.runtime.sendMessage({
          type: 'storage_set',
          data: {'esp_accounts': newList}
        }, (() => {
          this.setState({esp_accounts: items['esp_accounts']});
        }).bind(this));
      }).bind(this)
    );
  }

  handleHostedTest() {
    window.postMessage({ method: 'checklist-checklistPage' }, '*');
  }

  componentDidMount() {
    this.unmounted = false;
    if (window.chrome && chrome.runtime && chrome.runtime.id) {
      chrome.runtime.sendMessage({
          type: 'storage_get',
          data: ['esp_accounts']
        },
        ((items) => {
          if (items.hasOwnProperty('esp_accounts') && !this.unmounted){
            this.setState({esp_accounts: items['esp_accounts']});
          }
        }).bind(this)
      );
    }
  }

  componentWillMount() {
    window.addEventListener('message', this);
  }

  componentWillUnmount() {
    this.unmounted = true;
    window.removeEventListener('message', this);
  }

  handleEvent(message) {
    if (message.type !== 'message') { return; }
    switch (message.data.method) {

      case 'checklist-espAccountUpdated':
        this.setState({
          esp_accounts: message.data.esp_accounts
        });
        break;
    }
  }

  calculateWelcomeMessage() {
    let currentHours = new Date().getHours();
    if (currentHours < 12 ) {
      return "Good Morning!";
    } else if (currentHours >= 12 && currentHours < 18 ) {
      return "Good Afternoon!";
    } else if (currentHours >= 18 && currentHours < 24 ) {
      return "Good Evening!";
    }
  }
}



