import { Component } from 'react';
import PropTypes from 'prop-types';


export default class UpgradeCTA extends Component {

  render(){
    return (
      <div className={`section-body animated fadeIn ${this.props.wide ? 'wide' : ''} ${this.props.containerClass}`}>
        <div className="free-trial-message">
          <h1>{this.props.title}</h1>
          <p>{this.props.description}</p>
          <a className="view-all-previews-btn" href={this.props.href} target="_blank">
            <span className="button-text">{this.props.buttonText || 'Upgrade your plan'}</span>
          </a>
        </div>
      </div>
    );
  }
}

UpgradeCTA.propTypes = {
  wide: PropTypes.bool,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  buttonText: PropTypes.string,
  href: PropTypes.string.isRequired,
  containerClass: PropTypes.string
};



