import { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../../../components/header';
const Litmus = require('../../../../../base/litmus');

export default class ReadOnlyCtaPage extends Component {
  static get propTypes() {
    return {
      currentAccount: PropTypes.number,
      accounts: PropTypes.arrayOf(PropTypes.object),
      mode: PropTypes.string,
      currentEsp: PropTypes.object.isRequired,
    };
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  onClickLogin(e) {
    e.preventDefault();
    window.parent.postMessage({ method: 'checklist-collapseFullScreen' }, '*');

    Litmus.goToPage('login');
  }

  render() {
    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          mode={this.props.mode}
        />

        <div className="sections">
          <div className="section dashboard expanded">
            <div className="section-body animated fadeIn">
              <div className="free-trial-message">
                <div className="extension-heading">
                  <h2>Litmus Extension</h2>

                  <p>Test with Litmus wherever you build</p>

                  <hr />
                </div>

                <h1>You’re logged in as a Read-Only user</h1>
                <p>Read-Only users do not have permission to access the Litmus Extension. Access is only available to Active Users, Admins, or Account Holders.
</p>
                <p>Please <a>contact your Account Holder</a> if you would like to obtain access.</p>

                <hr />

                <span className="button-text">Not you? <a href="#" onClick={this.onClickLogin.bind(this)}>Sign in under a different account</a>.</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



