import React from 'react';
import jQuery from 'jquery';

window.React = React;

const handleChange = Symbol('handleChange');

export default class App extends React.Component {
	constructor() {
		super();
		window.onhashchange = this[handleChange].bind(this);
	}

	[handleChange](event) {
		let hash, hashParts, pageName;

		// get previous page
		let prevPage = this.state.page;

		const url = event ? event.newURL : window.location.href;
		const hashPos = url.indexOf('#');

		if (hashPos !== -1) {
			hash = hashPos ? url.substr(hashPos) : '';
			hashParts = hash.split('/');
			pageName = (hashParts.shift() || '').substr(1);
			hashParts = hashParts.join('/');
		}

		// set body class
    jQuery('body').attr('class', pageName);

		//console.info('Handling page change to', pageName || 'default', hashParts);
		window.parent.postMessage({
      method: 'checklist-pageChange',
      page: pageName,
      path: hashParts
    }, '*');

		const PageClass = this.getPageClassByName(pageName);

		if (!PageClass) {
			throw new Error('Can\'t find page class');
		}

		if (this.willLeavePage) {
			this.willLeavePage(this.state.pageName);
		}

		this.setState({
			pageName: pageName,
			page: PageClass,
			previousPage: prevPage,
			path: hashParts
		});

		if (this.didUpdatePage) {
			this.didUpdatePage(pageName);
		}

		// clear previousPage after transition
		setTimeout(() => {
			this.setState({
				previousPage: null
			});
		}, 2000);
	}

	// Override in App subclass
	getPageClassByName(pagename) {
		return null;
	}

	componentDidMount() {
		this[handleChange]();
	}

	componentWillUnmount() {
		window.onhashchange = null;
	}

	componentWillMount() {
		window.onhashchange = this[handleChange].bind(this);
	}
}



