import { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../../../components/header';

export default class ExtensionUpgradeCtaPage extends Component {
  static get propTypes() {
    return {
      currentAccount: PropTypes.number,
      accounts: PropTypes.arrayOf(PropTypes.object),
      mode: PropTypes.string,
      currentEsp: PropTypes.object.isRequired,
      calloutLink: PropTypes.string,
      recordInSegment: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
  }

  onCalloutClick(e) {
    e.preventDefault();

    if (!this.props.calloutLink) {
     return;
    }
    window.open(this.props.calloutLink + '#enterprise', '_blank');
    this.props.recordInSegment('Billing link clicked');
  }

  ctaDescription() {
    if (this.props.currentEsp.api_code && this.props.currentEsp.api_code.match(/ibmwatson|salesforce|responsys|marketo|eloqua/)) {
      return (<p>To run a Litmus Checklist directly in your favorite email service provider or utilize Litmus Email Previews alongside your local editor, upgrade to Litmus Enterprise.</p>);
    } else if (this.props.currentEsp.api_code && this.props.currentEsp.api_code.match(/mailchimp|campaignmonitor/)) {
      return (<p>To run a Litmus Checklist directly in Campaign Monitor or MailChimp or utilize Litmus Email Previews alongside your local editor, upgrade to a Plus Plan.</p>);
    } else if (this.props.currentEsp && !this.props.currentEsp.name) {
      return (<p>To run a Litmus Checklist directly in Campaign Monitor or MailChimp or utilize Litmus Email Previews alongside your local editor, upgrade to a Plus Plan.</p>);
    } else {
      return (<p>To run a Litmus Checklist directly in your email service provider or utilize Litmus Email Previews alongside your local editor, upgrade to a Litmus Plus, Pro, or Enterprise Plan.</p>);
    }
  }

  ctaContent() {
    if (this.props.currentEsp.api_code && this.props.currentEsp.api_code.match(/ibmwatson|salesforce|responsys|marketo|eloqua/)) {
      return (
        <div>
          <p>With Litmus Enterprise you can say goodbye to navigating between your email building tool and Litmus with the Litmus Extension. Plus, get access to all of the powerful functionality of the Litmus Enterprise platform.</p>

          <a className="btn btn-primary btn-lg btn-block" href="#" onClick={this.onCalloutClick.bind(this)}>
            <span className="button-text">Learn more about Litmus Enterprise →</span>
          </a>
        </div>
      );
    } else if ((this.props.currentEsp && this.props.currentEsp.name) || (this.props.calloutLink && this.props.calloutLink.includes('localhtmleditor'))) {
      return (
        <div>
          <p>Upgrade to Litmus Plus today and say goodbye to navigating between your email building tool and Litmus with the Litmus Extension. Plus, get access to Spam Testing, Email Analytics, and more. </p>

          <a className="btn btn-primary btn-lg btn-block" href="#" onClick={this.onCalloutClick.bind(this)}>
            <span className="button-text">Upgrade + get access to Litmus Extension →</span>
          </a>
        </div>
      );
    } else {
      return (
        <div className="upsell-list">
          <h3>Litmus Extension availability:</h3>

          <h4>Litmus Plus + Litmus Pro</h4>
          <ul>
            <li>All desktop editors, task runners + localhosts</li>
            <li>Campaign Monitor</li>
            <li>MailChimp</li>
          </ul>

          <h4>Litmus Enterprise</h4>
          <ul>
            <li>All desktop editors, task runners + localhosts</li>
            <li>Campaign Monitor</li>
            <li>MailChimp</li>
            <li>Eloqua</li>
            <li>Klaviyo</li>
            <li>Marketo</li>
            <li>Responsys</li>
            <li>Acoustic Campaign</li>
            <li>Pardot</li>
            <li>Salesforce Marketing Cloud (formerly ExactTarget)</li>
          </ul>

          <a className="btn btn-primary btn-lg btn-block" href="#" onClick={this.onCalloutClick.bind(this)}>
            <span className="button-text">Upgrade + get access to Litmus Extension → </span>
          </a>
        </div>
      );
    }
  }

  render() {
    let espCallout = this.props.currentEsp.name ? `Testing inside ${this.props.currentEsp.name}` : 'Testing';
    let ctaContent = this.ctaContent();
    let ctaDescription = this.ctaDescription();
    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          mode={this.props.mode}
        />

        <div className="sections">
          <div className="section dashboard expanded">
            <div className="section-body animated fadeIn">
              <div className="free-trial-message">
                <div className="extension-heading">
                  <h2>Litmus Extension</h2>

                  <p>Test with Litmus wherever you build</p>

                  <hr />
                </div>

                <h1>Your Litmus plan does not include access to the Litmus Extension</h1>

                <div className="playerContainer">
                  <iframe src="https://fast.wistia.net/embed/iframe/azplvsa127" width="100%" height="233px" style={{ position: 'absolute', top: 0, bottom: 0, left: 0, right: 0 }}/>
                </div>
                {
                  ctaDescription
                }

                {
                  ctaContent
                }

              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



