import { Component } from 'react';
import PropTypes from 'prop-types';
import EllipsisSpan from '../ellipsis_span';

export default class ImageRow extends Component {


  static propTypes = {
    alt_text: PropTypes.string,
    url: PropTypes.string.isRequired
  }

  render() {
    const parsedUrl = document.createElement('a');
    parsedUrl.href = this.props.url;

    const imageFilename = parsedUrl.pathname.split('/').pop();

    let altText = this.props.alt_text;
    let status = '';
    if (this.props.alt_text === null) {
      altText = 'Missing';
    } else if (this.props.alt_text === '') {
      altText = 'Empty';
    }

    return (
      <div className={'data-row ' + status}>
        <table className={'data-row-table'}>
          <tbody>
            <tr>
              <th>Alt Text:</th>
              <td className={'link-text ' + status}>{altText}</td>
            </tr>
            <tr>
              <th>Image:</th>
              <td className={'link-path'}>
                <EllipsisSpan className={'link-path filename hint--bottom-left hint--rounded hint--no-animate'} maxLength={38} data-hint={imageFilename}>{imageFilename}</EllipsisSpan>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }

}



