export default class Evergage {
  static initialize() {
    var _aaq = window._aaq || (window._aaq = []);
    var evergageAccount = 'litmus';
    var dataset = 'engagenext';
    _aaq.push(['setEvergageAccount', evergageAccount], ['setDataset', dataset], ['setUseSiteConfig', true]);

    (function(){
     var d = document, g = d.createElement('script'), s = d.getElementsByTagName('script')[0];
         g.type = 'text/javascript'; g.defer = true; g.async = true;
         g.src = 'https://cdn.evergage.com/beacon/'
                 + evergageAccount + '/' + dataset + '/scripts/evergage.min.js';
         s.parentNode.insertBefore(g, s);
    })();
  }
}



