import { Component } from 'react';
import PropTypes from 'prop-types';

import Litmus from '../../../../base/litmus';

export default class LoginPage extends Component {

  static propTypes = {
    mode: PropTypes.string
  };

  constructor(props) {
    super(props);

    this.state = {
      error: null,
      email: '',
      password: ''
    };
  }

  onFormSubmit(e) {
    e.preventDefault();
    const email = this.state.email;
    const password = this.state.password;
    Litmus.login(email, password, this.loginSuccess.bind(this), this.loginFail.bind(this));
    this.setState({error: null});

  }

  handleChange(field, event) {
    let obj = {};
    obj[field] = event.target.value;
    this.setState(obj);
  }

  loginSuccess(data) {
    chrome.runtime.sendMessage({ type: 'storage_set', data: { accounts: data.accounts, current_account: 0 } }, function() {
      window.postMessage({ method: 'login-status', loggedIn: true, currentAccount: 0, accounts: data.accounts, newLogin: true }, '*');
    });

    // window.location.hash = 'checklist';
  }

  loginFail() {
    this.setState({
      error: 'The email address or password you entered was invalid.'
    });
  }

  onClickClose(e) {
    e.preventDefault();
    Litmus.goToPage(this.props.mode);
  }

  render() {
    return (
      <div className={'page-content login'}>
        <header id="litmus-header" className="fixed-top back-header">
          <a href="#" className="back-icon icon-chevron-left" onClick={this.onClickClose.bind(this)}></a>
          <img src="/images/litmus-logo-wheel.svg" alt="Litmus" className="litmus-header__logo" />
        </header>

        <form method="post" action="#" id="login-form" onSubmit={this.onFormSubmit.bind(this)}>
          <div className="login-form">

            <div className="login-wrapper">
              <h4>Don’t have an account yet?</h4>
              <p>Get up and running in seconds, and join the thousands of global brands that trust Litmus to maximize productivity.</p>
              <a className="btn btn-secondary btn-block" href="https://litmus.com/extension" target="_blank">Learn more + sign up →</a>

              {this.state.error ?
                <div id="login-failure">{this.state.error}</div>
                : ''
              }

              <h4>Already a member? Sign in to your account</h4>
              <div className="form-row">
                <label className="sr-only" htmlFor="email">Email Address</label>
                <input type="email" name="email" id="email" placeholder="Email address" onChange={this.handleChange.bind(this, 'email')}/>
              </div>

              <div className="form-row">
                <label className="sr-only" htmlFor="password">Password</label>
                <input type="password" name="password" id="password" placeholder="Password" onChange={this.handleChange.bind(this, 'password')}/>
              </div>

              <input type="submit" value="Sign in" className="btn btn-primary btn-block" />

              <a className="forgot-password" href="https://litmus.com/password/forgot" target="_blank">Forgot password?</a>
            </div>

          </div>

        </form>
      </div>
    );
  }
}



