import { Component } from 'react';
import PropTypes from 'prop-types';

export default class AccountButton extends Component {
  static propTypes = {
    onClick: PropTypes.func,
    accountName: PropTypes.string,
    selected: PropTypes.bool
  };

  render() {
    return (
      <a className="account-button" href="#" onClick={this.props.onClick}>
        <span className="button-text">{this.props.accountName}</span>
        <span className={'circle-icon ' + (this.props.selected ? 'success' : '')} />
      </a>
    );
  }
}




