// Copied and modified from: http://stackoverflow.com/a/13293684/304706

export default class StickyFooter {
  static load() {
    const $window = this.$window = $(window);

    this.unload();

    $('.sections').on('scroll.stickyfooter', this.onScroll.bind(this));
    this.onScroll();
  }

  static unload() {
     $('#url-footer').removeClass('sticky');

    this.$window.off('scroll.stickyfooter');
  }

  static onScroll() {
    const scrollTop       = $('.sections').scrollTop();
    const whatsNextButton = $('.whats-next .btn-primary');
    const offsetHeight    = $('.sections')[0].offsetHeight;
    const threshold       = $(whatsNextButton)[0].offsetTop;
    const buttonHeight    = $(whatsNextButton)[0].offsetHeight;
    const windowScrollTop = $('html').scrollTop();
    const manualYOffset   = 18;

    if ((scrollTop + offsetHeight + windowScrollTop) >= (threshold + buttonHeight + manualYOffset)) {
      $('#url-footer').addClass('sticky');
    } else {
      $('#url-footer').removeClass('sticky');
    }
  };
}



