import React from 'react';
import { Component } from 'react';
import PropTypes from 'prop-types';

import SectionHeader from '../sectionHeader';
import ActivationCTA from './activationCTA';


export default class BaseSection extends Component {

  renderHeader() {
    return (
      <SectionHeader
        skipFirstImpressions={this.props.skipFirstImpressions}
        onHeaderUpdate={this.props.onHeaderUpdate}
        issuesCount={this.props.issuesCount}
        approved={this.props.approved}
        toggleApproved={this.props.toggleApproved}
        name={this.constructor.sectionName}
        slug={this.constructor.sectionSlug}
        local={this.props.local}
      />
    );
  }

  render() {
    if (this.props.visible === false) {
      return null;
    }

    const classNames = [
      'section',
      this.constructor.sectionSlug,
      this.props.approved ? 'collapsed' : 'expanded'
    ].join(' ');

    return (
      <div className={classNames}>
        { this.props.local ? '' : this.renderHeader() }

        { this.props.approved ? '' : this.renderSectionBody() }
      </div>
    );
  }

  renderSectionBody() {
    throw new Error('Subclass must override content()!');
  }

  renderActivationCTA(message, userType) {
  	let activationCTA = null;
  	if (userType !== 'paidUser' || this.props.isEnterpriseLocked) {
  		activationCTA = <ActivationCTA activationMessage={message} userType={userType}/>;
  	}
  	return activationCTA;
  }
}

BaseSection.propTypes = {
  skipFirstImpressions: PropTypes.bool,
  onHeaderUpdate: PropTypes.func,
  issuesCount: PropTypes.number,
  approved: PropTypes.bool,
  toggleApproved: PropTypes.func,
  local: PropTypes.bool,
  visible: PropTypes.bool,
  isEnterpriseLocked: PropTypes.bool,
  currentEsp: PropTypes.object
};



