import { Component } from 'react';
import PropTypes from 'prop-types';


export default class AuthenticatedESP extends Component {

  static get propTypes() {
    return {
      email: PropTypes.string,
      imgUrl: PropTypes.string,
      campaign_url: PropTypes.string,
      removeHandler: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
  }

  render() {
    let email = this.props.email;
    return (
      <div className="esp-panel">
        <div className="esp-logo">
          <img src={this.props.imgUrl} alt="" />
        </div>
        <div className="esp-info">
          <h4>{email}</h4>
          <a href={this.props.campaign_url} target="_blank">Visit draft email campaigns</a>
        </div>
        <div>
          <a className="has-icon" onClick={this.props.removeHandler}>
            <span className="feathericon-trash"></span>
          </a>
        </div>
      </div>
    );
  }
}





