import React, { Component } from 'react';
import PropTypes from 'prop-types';
import StickyFooter from './stickyFooter';

export default class UrlFooter extends Component {

  static get propTypes() {
    return {
      onClickUrl: PropTypes.func
    };
  }

  render() {
    return (
      <footer id="url-footer" className="bottom-footer">
          <a id="urlButton" href="#" className="btn btn-primary" onClick={this.props.onClickUrl}>
            Open in Litmus
          </a>
      </footer>
    );
  }

  componentDidMount() {
    StickyFooter.load();
  }
}





