import { Component } from 'react';
import PropTypes from 'prop-types';
import ESPSelectionList from './espSelectionList';
import ESPSelectItem from './espSelectItem';


export default class AuthenticatedESPSection extends Component {

  static get propTypes() {
    return {
      esp_accounts: PropTypes.arrayOf(PropTypes.object),
      supportedESPs: PropTypes.arrayOf(PropTypes.object),
      calloutLink: PropTypes.string,
      recordInSegment: PropTypes.func
    };
  }

  constructor(props) {
    super(props);
    this.state = {visible: false};
  }

  onClickAdd(e) {
    e.stopPropagation();
    this.setState({
      visible: !this.state.visible
    });
  }

  render() {
    let currentEspNames = this.props.esp_accounts.map( (esp) => esp.name );

    // let listToRender = this.props.supportedESPs.filter( (item) => { return !currentEspNames.includes(item.name) });
    let listToRender = this.props.supportedESPs;

    return (
      <div>
        <div className="panel esp-select">
          {
            this.props.children
          }
          <div className="centered">
            <a onClick={this.onClickAdd.bind(this)} style={ { cursor: 'pointer'} }>Add another ESP</a>
          </div>
        </div>
        <ESPSelectionList hide={!this.state.visible} initial={false}>
          {
            listToRender.map( (esp) => {
              return <ESPSelectItem {...esp} key={esp.name} upgrade_url={this.props.calloutLink} recordInSegment={this.props.recordInSegment}/>;
            })
          }
        </ESPSelectionList>
      </div>
    );
  }
}





