import PropTypes from 'prop-types';

import BaseSection from '../baseSection';

import TrackingRow from './trackingRow';

import CTA from '../cta';

export default class TrackingSection extends BaseSection {
  static sectionName = 'Tracking'
  static sectionSlug = 'tracking'

  static propTypes = {
    links: PropTypes.array.isRequired,
    userType: PropTypes.string
  }

  renderSectionBody() {
    const trackedLinks = (this.props.links || []).filter((l) => l.campaign || l.medium || l.source);
   	let messageForActivationCTA = 'Verify that your clickthroughs are being tracked by Litmus or your email marketing software.';
    let paidPlan = this.props.userType === 'paidUser';

    return (
      <div className="section-body animated fadeIn">
      	{
      		this.renderActivationCTA(messageForActivationCTA, this.props.userType)
      	}
      	<div className={paidPlan ? '' : 'blurred-content' }>
	        {
	          trackedLinks.length === 0 ?
              <CTA
                title="Gain insight into where your subscribers are opening your email."
                link="https://litmus.com/analytics/new"
                linkText="Add Litmus Analytics"
              />
	          :
	          <div className="panel data-panel">
	            <div className="data-rows">
	              {trackedLinks.map((link, i) =>
	                <TrackingRow {...link} key={i} />
	              )}
	            </div>
	          </div>
	        }
	    </div>
      </div>
    );
  }

}



