import React, { Component } from 'react';
import PropTypes from 'prop-types';
const Litmus                  = require('../../../../../base/litmus');

import ImageToggleButton from './imageToggleButton';

export default class PreviewHeader extends Component {

  static propTypes = {
    expandedFullScreen: PropTypes.bool,
    previews: PropTypes.arrayOf(PropTypes.object),
    index: PropTypes.number,
    lastInstantApiDate: PropTypes.instanceOf(Date),
    imagesEnabled: PropTypes.bool,
    retestFn: PropTypes.func,
    local: PropTypes.bool,
    recordInSegment: PropTypes.func
  };


  onClickExpandToggle(e) {
    e.preventDefault();

    this.props.expandedFullScreen ? this.collapse(true) : this.expand(true);
  }

  onClickImagesToggle(e) {
    e.preventDefault();
    var enabled = this.props.imagesEnabled;

    if (enabled) {
      window.postMessage({ method: 'checklist-imagesDisable'}, '*');
    } else {
      window.postMessage({ method: 'checklist-imagesEnable'}, '*');
    }

  }

  expand(persist) {
    window.parent.postMessage({ method: 'checklist-expandFullScreen', persist: !!persist }, '*');
  }

  collapse(persist) {
    window.parent.postMessage({ method: 'checklist-collapse', persist: !!persist }, '*');
  }

  onClickPrev(e) {
    e.preventDefault();
    if (this.prevDisabled()) {
      return;
    }

    const newIndex = this.props.index - 1;
    Litmus.goToPage(`#preview/${newIndex}`);

    if (this.props.recordInSegment) {
      this.props.recordInSegment('Single-Preview Previous clicked');
    }
  }

  onClickNext(e) {
    e.preventDefault();
    if (this.nextDisabled()) {
      return;
    }

    const newIndex = this.props.index + 1;
    Litmus.goToPage(`#preview/${newIndex}`);

    if (this.props.recordInSegment) {
      this.props.recordInSegment('Single-Preview Next clicked');
    }
  }

  onClickClose(e) {
    e.preventDefault();
    this.collapse();

    // Before closing, check if we ran an Instant API while showing the full preview page.
    // If so, we need to run a retest before going back to the checklist.
    if (this.props.lastInstantApiDate !== null) {
      this.props.retestFn();
    }

    sessionStorage.removeItem('currentHash');
    Litmus.goToPage('checklist');
    if (this.props.local) {
      chrome.runtime.sendMessage({ type: 'test_lifecycle', data: { status: 'localActive' } });
    }
  }

  prevDisabled() {
    return this.props.index === 0;
  }

  nextDisabled() {
    return this.props.index === this.props.previews.length - 1;
  }

  render() {
    const preview = this.props.previews[this.props.index];

    const expandIconClass = this.props.expandedFullScreen ? 'feathericon-minimize' : 'feathericon-maximize';
    const imagesClass   = this.props.imagesEnabled ? 'selected' : '';
    const imageToggle   = !!preview.screenshots.full_no_images ? <ImageToggleButton tooltip="toggle images on/off" href="#" id="image-toggle-tooltip" className={`feathericon-image icon-button ${imagesClass}`} onClick={this.onClickImagesToggle.bind(this)}></ImageToggleButton> : null;

    return (
      <div className="preview-header">

        <a href="#" className="close-action" onClick={this.onClickClose.bind(this)}>
          <i className="icon-chevron-left"></i>
        </a>

      	<div className="client">{preview.client} <span>({preview.os})</span></div>

        <div className="arrows">
          {imageToggle}
          <a href="#" className={'arrow icon-chevron-left' + (this.prevDisabled() ? ' disabled' : '')} onClick={this.onClickPrev.bind(this)} />
          <a href="#" className={'arrow icon-chevron-right' + (this.nextDisabled() ? ' disabled' : '')} onClick={this.onClickNext.bind(this)} />
        </div>
      </div>
    );
  }

  componentDidMount() {
    jQuery('body').on('keydown.previewHeader', (e) => {
      switch (e.which) {
        case 37: // left
          this.onClickPrev(e);
          break;
        case 39: // right
          this.onClickNext(e);
          break;
      }
    });
  }

  componentWillUnmount() {
    jQuery('body').off('keydown.previewHeader');
  }
}



