import PropTypes from 'prop-types';
import PureComponent from 'react-pure-render/component';

export default class PreviewThumb extends PureComponent {

  enabledPreview() {
    const thumbnailURL = this.props.screenshots.large_thumbnail.replace(/^\/\//, 'https://');
    
    return (
      <div className="preview-img-container">
        <div className="image-spinner small"></div>

        { this.props.ready ?
          <img
            className="preview-img"
            key={thumbnailURL}
            src={thumbnailURL} />
          : '' }

        <div className="preview-hover"></div>
      </div>
    );
  }

  disabledPreview() {
    const thumbnailURL = this.props.screenshots.large_thumbnail.replace(/^\/\//, 'https://');

    return (
      <div className="preview-img-container">
        <img
          className="preview-img blurred-content"
          key={thumbnailURL}
          src={thumbnailURL} />

        <div className="">
          <a href="#login" className="activate-btn">
            <i className="feathericon-lock"></i>
          </a>
        </div>
      </div>
    );
  }

  render() {
    return (
      <li className="preview-thumb" onClick={this.props.onClick}>
        {this.props.restricted ? this.disabledPreview() : this.enabledPreview()}

        <div className="preview-info">
          <span>{this.props.client}</span>
        </div>
      </li>
    );
  }

}

PreviewThumb.propTypes = {
  onClick: PropTypes.func.isRequired,
  client: PropTypes.string.isRequired,
  screenshots: PropTypes.object.isRequired,
  restricted: PropTypes.bool.isRequired,
  ready: PropTypes.bool
};



