import { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../../components/header';
import NewSecondFactorForm from './newSecondFactorForm';

export default class NewSecondFactorPage extends Component {
  static get propTypes() {
    return {
      currentAccount: PropTypes.number,
      accounts: PropTypes.arrayOf(PropTypes.object),
      mode: PropTypes.string,
      mfaError: PropTypes.string,
      mfaSuccess: PropTypes.bool,
      mfaNewCode: PropTypes.bool
    };
  }

  constructor(props) {
    super(props);
    this.state = {
      completed: false
    };
    this.onNewCodeRequest = this.onNewCodeRequest.bind(this);
    this.onCodeSubmit     = this.onCodeSubmit.bind(this);
  }

  onNewCodeRequest() {
    window.postMessage({ method: 'checklist-show-new-second-factor' }, '*');
  }

  onCodeSubmit(data) {
    window.postMessage({ method: 'checklist-second-factor-verify', data: data }, '*');
  }

  render() {
    let flash = '';
    if (this.props.mfaNewCode) {
      flash = <div className="alert alert-success">2-step verification is now enabled!</div>;
    } else if (this.props.mfaError) {
      flash = <div className="alert alert-error">{this.props.mfaError}</div>;
    }

    return (
      <div className="checklist">
        <Header
          accounts={this.props.accounts}
          currentAccount={this.props.currentAccount}
          mode={this.props.mode}
        />

        <div className="sections">
          <div className="section dashboard expanded">
            <div className="section-body">
              <div className="signup signup-narrow">
                <div className="signup-head">
                  <div className="signup-logo">
                    <a href="http://litmus.com">
                      <img alt="Litmus" src="https://master.litmusdev.com/assets/signup/litmus-logo-242fd16d7e031aeb5ab1852fe78e8877.png" />
                    </a>
                  </div>
                  <div className="signup-caption">
                    <h1>2-step verification</h1>
                  </div>
                  <div className="clearfix"></div>
                </div>
                <div className="signup-form">
                  <div className="wrapper has-hr">
                    {flash}
                    <NewSecondFactorForm onNewCodeRequest={this.onNewCodeRequest} onCodeSubmit={this.onCodeSubmit} />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


