import PropTypes from 'prop-types';

import BaseSection from '../baseSection';
import ImageRow from './imageRow';

export default class ContentSection extends BaseSection {
  static sectionName = 'Image Blocking'
  static sectionSlug = 'content'


  static propTypes = {
    previewUrl: PropTypes.string.isRequired,
    imageTags: PropTypes.array.isRequired,
    userType: PropTypes.string
  }

  onClickContentPreview(e) {
    e.preventDefault();
    Litmus.goToPage('contentPreview');
  }

  renderSectionBody() {
  	let messageForActivationCTA = 'Preview your email with Images off-and get notified about which images are missing alt text';
    let paidPlan = this.props.userType === 'paidUser';

    return (
      <div className="section-body animated fadeIn">
      	{
      		this.renderActivationCTA(messageForActivationCTA, this.props.userType)
      	}
      	<div className={ paidPlan ? '' : 'blurred-content' }>
	        <div className="panel data-panel content-panel">
	          <div className="content-container" onClick={this.onClickContentPreview}>
	            <img key={this.props.previewUrl} src={this.props.previewUrl} />
	          </div>

	          <div className="data-rows">
	            {this.props.imageTags.map((image, i) =>
	              <ImageRow {...image} key={i} />
	            )}
	          </div>
	        </div>
	    </div>
      </div>
    );
  }

}



