import PropTypes from 'prop-types';

import BaseSection from '../baseSection';

import LinkRow from './linkRow';

export default class LinksSection extends BaseSection {
  static sectionName = 'Link Check'
  static sectionSlug = 'links'

  static propTypes = {
    link_checks: PropTypes.array.isRequired,
    userType: PropTypes.string
  }

  renderSectionBody() {
  	let messageForActivationCTA = 'Never send an email with broken links again- ensure they are working and going to the Intended destination.';
    let paidPlan = this.props.userType === 'paidUser';

    return (
      <div className="section-body animated fadeIn">
      	{
      		this.renderActivationCTA(messageForActivationCTA, this.props.userType)
      	}
      	<div className={paidPlan ? '' : 'blurred-content' }>
	        <div className="panel data-panel">
	          <div className="data-rows">
	            {this.props.link_checks.map((link, i) =>
		          <LinkRow {...link} status={link.valid ? 'success' : 'error'} key={i} />
		        )}
		      </div>
	        </div>
	    </div>
      </div>
    );
  }

}



