import React from 'react';
import { Component } from 'react';
import PropTypes from 'prop-types';


export default class ESPSelectionList extends Component {

  static get propTypes() {
    return {
      initial: PropTypes.bool,
      hide: PropTypes.bool
    };
  }

  constructor(props) {
    super(props);

  }

  render() {
    let addESPText = this.props.initial ? '' : <p>Add a new ESP (2):</p> ;
    return (
      <div className={this.props.hide ? 'unslide' : 'slide'}>
        { addESPText }
        <div className="grid">
          <ul>
            {
              this.props.children
            }
          </ul>
        </div>
      </div>
    );
  }
}



