export default class ReadableFileSize {
  static calculate(size) {
    let i = 0;
    const byteUnits = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    while (size > 1024) {
      size = size / 1024;
      i++;
    };

    return {
      size: parseFloat(size.toFixed(1)),
      unit: byteUnits[i]
    };
  }
}



