import PropTypes from 'prop-types';

import BaseSection from '../baseSection';
import SummaryPanel from './summaryPanel';
import ImageRow from './imageRow';
import decimalAdjust from './decimalAdjust';

export default class ImagesSection extends BaseSection {
  static sectionName = 'Loading Speed'
  static sectionSlug = 'images'


  static propTypes = {
    imagesResult: PropTypes.object.isRequired,
    userType: PropTypes.string
  }

  renderSectionBody() {
    let loadTime = 0;

    this.props.imagesResult.image_checks.forEach(check => {
      const startTime = new Date(check.started);
      const endTime = new Date(check.completed);
      const time = (endTime - startTime) / 1000;

      loadTime += time;
    });

    let messageForActivationCTA = 'Identify broken or slow loading images';
    let paidPlan = this.props.userType === 'paidUser';

    return (
      <div className="section-body animated fadeIn">
      	{
      		this.renderActivationCTA(messageForActivationCTA, this.props.userType)
      	}
      	<div className={paidPlan ? '' : 'blurred-content' }>
	        <p className="well load-time-panel">{this.descriptionForLoadTime(loadTime)}</p>

	        <SummaryPanel images_result={this.props.imagesResult} loadTime={decimalAdjust('ceil', loadTime, -1)} />

	        <div className="panel data-panel">
	          <table className="data-row-table horizontal-table">
	            <thead>
	              <tr>
	                <th className="link-path">Image</th>
	                <th className="image-size">Size</th>
	                <th className="image-time">Time</th>
	              </tr>
	            </thead>
	            <tbody>
		            {this.props.imagesResult.image_checks.map((check, i) =>
		              <ImageRow check={check} key={i} />
		            )}
	            </tbody>
	          </table>
	        </div>
	    </div>
      </div>
    );
  }

  descriptionForLoadTime(loadTime) {
    if (loadTime < 2.0) {
      return `Your email took ${loadTime.toFixed(2)} seconds to open - great job!`;
    } else if (loadTime >= 2.0 && loadTime < 10.0) {
      return `Your email took ${loadTime.toFixed(2)} seconds to open. \
              This is on the high end of open times, and is likely to impact reader engagement. \
              Because open times do vary depending on the reader's environment, \
              and the performance of the image servers, we recommend keeping load times \
              below 2 seconds to ensure the recipient isn't waiting too long \
              for the email to fully open.`;
    } else {
      return `Your email took ${loadTime.toFixed(2)} seconds to open. \
              This is very high, and is likely to result in poor reader engagement. \
              We recommend keeping load times below 2 seconds to ensure the recipient isn't \
              waiting too long for the email to fully open.`;
    }
  }

}



