var React = require('react');
import PropTypes from 'prop-types';

import BaseSection from '../baseSection';

import PreviewThumb from './previewThumb';

import UpgradeCTA from './upgradeCTA';
import TrialLimitMessage from './trialLimitMessage';

const Litmus = require('../../../../../../../base/litmus');

export default class PreviewsSection extends BaseSection {

  onClickPreviewThumb(index, e) {
    e.preventDefault();

    const preview = this.props.previews[index];

    // If this is a disabled preview, go to login page
    if (preview.restricted) {
      if (this.props.loggedIn) {
        window.open('https://litmus.com/billing?utm_medium=litmusextension', '_blank');
      } else {
        Litmus.goToPage('login');
      }
    } else {
      Litmus.goToPage(`preview/${index}`);
      window.location.hash = `preview/${index}`;
      if (this.props.local) {
        chrome.runtime.sendMessage({ type: 'test_lifecycle', data: { status: 'localActive' } });
      }
    }

    if (this.props.recordInSegment) {
      this.props.recordInSegment('Preview clicked', { clickedPreview: `${preview.client} (${preview.os})`  });
    }
  }

  onClickClients(e) {
    e.preventDefault();
    window.open('https://litmus.com/settings/clients?utm_medium=litmusextension', '_blank');
    this.props.recordInSegment('Configure email clients clicked');
  }

  limitExceeded() {
    let count      = this.props.previewsCount;
    let totalLimit = this.props.totalLimit;

    if (this.unlimited()) {
      return false;
    }
    return (count >= totalLimit);
  }

  unlimited() {
    return this.props.totalLimit === 0;
  }

  renderSectionBody() {
    let previews            = this.props.previews;
    let trial               = this.props.trial;
    let hasAccess           = this.props.hasAccess;
    let count               = this.props.previewsCount;
    let totalLimit          = this.props.totalLimit;
    let threshold           = 0.8;
    let totalSelectedCount  = previews.length;

    if (!this.props.showAllPreviews) {
      previews = previews.slice(0, 6);
    }

    let msg, containerClass, output, trialLimitMessage, linkText;

    if (!this.limitExceeded()) {
      if (trial) {
        msg = 'Want more than 50 previews? Activate your account';
      } else {
        if (((count / totalLimit) >= threshold ) && !this.unlimited()) {
          msg = 'You are nearing your limit.';
          linkText = 'Upgrade your plan';
          containerClass = 'nearing-limit';
        }
      }
    } else {
      let cta = trial ? 'Activate' : 'Upgrade';
      linkText = `${cta} your account`;
      msg = 'You have reached your preview limit.';
      containerClass = 'exceeded';
    }

    if (msg) {
      trialLimitMessage = (
        <TrialLimitMessage message={msg} linkText={linkText} count={count} totalLimit={totalLimit} containerClass={containerClass} calloutLink={this.props.calloutLink} recordInSegment={this.props.recordInSegment} />
      );
    }

    if (!hasAccess && this.props.userType === 'paidUser') {
      let href = this.props.userType === 'nonUser' ? 'https://litmus.com/signup/basic-plan?utm_medium=litmusextension' : 'https://litmus.com/billing?utm_medium=litmusextension';
      output = (
        <UpgradeCTA
          href={href}
          title="Good Morning!"
          wide={this.props.wide}
          description="To start testing your email in 70+ email apps and clients with the Litmus Extension, please upgrade to a Plus plan or above in your Litmus account."
        />
      );
    } else if (this.props.userType && this.props.userType === 'paidUser') {
        output = (
          <div className={`section-body animated fadeIn ${this.props.wide ? 'wide' : ''}`}>

            {trialLimitMessage}

            <p className="client-counter"><strong>{totalSelectedCount}</strong> out of <strong>{this.props.totalClientCount}</strong> clients selected. &nbsp;
              <a className="configure-clients" href="https://litmus.com/settings/clients?utm_medium=litmusextension" target="_blank">
                <span>
                  Change
                </span>
              </a>
            </p>

            <ul className="preview-thumbs">
              {previews.map((preview, i) =>
                <PreviewThumb
                  onClick={this.onClickPreviewThumb.bind(this, i)}
                  key={i}
                  client={`${preview.client} (${preview.os})`}
                  screenshots={preview.screenshots}
                  restricted={preview.restricted}
                  ready={preview.ready}
                />
              )}
            </ul>

            {this.props.showAllPreviews ? '' :
              <a className="btn btn-secondary btn-lg btn-block" href="#" onClick={this.props.onClickShowAllPreviews}>
                <span className="button-text">View all Email Previews</span>
              </a>
            }
          </div>
        );
    } else {
      let href = this.props.userType === 'nonUser' ? 'https://litmus.com/signup/basic-plan?utm_medium=litmusextension' : 'https://litmus.com/billing?utm_medium=litmusextension';
      output = (
        <UpgradeCTA
          href={href}
          title="Good Morning!"
          wide={this.props.wide}
          description="To start testing your email in 70+ email apps and clients with the Litmus Extension, please start a free Litmus trial."
        />
      );
    }

    return (output);
  }

}

PreviewsSection.propTypes = {
  previews: PropTypes.array.isRequired,
  loggedIn: PropTypes.bool,
  onHeaderUpdate: PropTypes.func,
  recordInSegment: PropTypes.func,
  approved: PropTypes.bool,
  toggleApproved: PropTypes.func,
  skipFirstImpressions: PropTypes.bool,
  showAllPreviews: PropTypes.bool,
  local: PropTypes.bool,
  wide: PropTypes.bool,
  totalClientCount: PropTypes.number,
  trial: PropTypes.bool.isRequired,
  hasAccess: PropTypes.bool.isRequired,
  previewsCount: PropTypes.number.isRequired,
  totalLimit: PropTypes.number.isRequired,
  calloutLink: PropTypes.string
};
PreviewsSection.sectionName = 'Email Previews';
PreviewsSection.sectionSlug = 'previews';



