import React from 'react';
import { Component } from 'react';
import PropTypes from 'prop-types';

export default class HostedTestOptions extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        <p>Open a draft email in your ESP or begin a test manually:</p>
        <a className="btn btn-primary btn-lg btn-block has-icon" onClick={this.props.handleHostedTest} target="_blank"><span className="feathericon-layout"></span>Run a Litmus test</a>
        <hr/>
      </div>
    );
  }
}

HostedTestOptions.propTypes = {
  handleHostedTest: PropTypes.func
};


